import React,{ useState,useEffect } from 'react'
import './Tabs.css'
const Tabs = ({children,current}) => {

  const [pos,setPos] = useState({left:0,width:0})
  const [currentTab,setCurrentTab] = useState(current||0)

  const handleTabsClick = tgt => {
      if (tgt.nodeName === 'LI') {
      const idx = Number(tgt.getAttribute('idx'))
      const left = tgt.offsetLeft
      const width = tgt.getBoundingClientRect().width
      setCurrentTab(idx)
      setPos({left,width})
    }
  }

  const setCurrent = tab =>{
    const tabList = document.getElementById('tabList')
    const elt = tabList.children[tab]
    const pos = elt.getBoundingClientRect().width
    setPos({left:elt.offsetLeft,width:pos})
  }

  useEffect(()=>{
    setCurrent(current ? current : 0)
    const tabList = document.getElementById('tabList')
    handleTabsClick(tabList.children[current])
  },[current])

  return (
    <main>
      <section className="Tabs">
        <ul onClick={e=>handleTabsClick(e.target)} id="tabList">
          {children.map((tab,i)=><li className={currentTab === i ? 'active' : ''} idx={i} key={'tab'+i}>{tab.props.title}</li>)}
        </ul>
        <span className="border" style={{transform:'translateX('+ pos.left +'px)','width':pos.width}}></span>
      </section>
      <section>
        {children[currentTab]}
      </section>
    </main>
  )
}

const Tab = (props,title) => <div>{props.children}</div>
export default Tabs
export { Tab }
