import React from 'react'
import Tabs,{ Tab } from '../../ui/Structure/Tabs'
import Payroll from '../Payroll'
import ReadyToPayRoll from '../Payroll/Dashboard/ReadyToPayRoll'
const Employees = ()=>{
  const isNewToPayroll = window.location.href.indexOf('?new=true') !== -1
  return (
    <main>
      <Tabs current="0">
        <Tab title="Payroll">
          {isNewToPayroll ? <ReadyToPayRoll/> : <Payroll/>}
        </Tab>
        <Tab title="Employees"><h1>Employees</h1></Tab>
      </Tabs>
    </main>
  )
}
export default Employees
