import React,{useContext} from 'react'
import { useHistory } from "react-router-dom"
import BackLink from '../../../ui/BackLink'
import ActionButton from '../../../ui/ActionButton'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PeriodSum from '../PeriodSum'

import './ConfirmHeader.css'

const ConfirmHeader = ()=> {
  const choices = ['Download CSV file','Delete payroll']
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const history = useHistory()
  const { base } = state
  const handleActionsClick = val =>{
    if (choices.indexOf(val) === 0) {
      dispatch({type:'DOWNLOAD_CSV'})
    } else {
      dispatch({type:'DELETE_PAYROLL'})
      setTimeout(()=>dispatch({type:'ANIMATE_PILE'}),5)
      history.push(base+'/employees')
    }
  }
  return (
    <main className="ConfirmHeader">
      <section>
        <main>
          <BackLink path="/employees/" txt="Payroll home"/>
          <h1>Payroll created</h1>
          <h2>Your payroll has been created.</h2>
        </main>
        <aside>
          <ActionButton
            label="Actions"
            options={choices}
            cb={handleActionsClick}/>
        </aside>
      </section>
      <PeriodSum/>
    </main>
  )
}
export default ConfirmHeader
