import React, { useContext } from 'react'
import DropDown from '../../../../ui/DropDown'
import './HistoryFilter.css'
import PayrollDispatchContext from '../../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../../context/Payroll/PayrollStateContext'

const PeriodOptions =()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  return (
    <DropDown>
      <ul className="historyFilterList">
        <li onClick={()=>dispatch({type: 'resetPeriod'})}>All time</li>
        {state.taxYears.map((year,idx)=>(
          <li
            key={idx}
            onClick={()=>dispatch({type: 'filterByTaxYear',payload:year})}>{year} tax year</li>
        ))}
      </ul>
    </DropDown>
  )
}

const PaySchedule =()=>{
  const dispatch = useContext(PayrollDispatchContext)
  return (
    <DropDown>
      <ul className="payScheduleOptions">
        <li onClick={()=>dispatch({type:'showAll'})}>All</li>
        <li onClick={()=>dispatch({type:'monthly'})}>Monthly</li>
        <li onClick={()=>dispatch({type:'weekly'})}>Weekly</li>
      </ul>
    </DropDown>
  )
}

const HistoryFilter =()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  return (
      <div className="HistoryFilter">
        {state.history.length ? (
          <article>
            <span>Pay history for</span>
            <button onClick={()=>dispatch({type:'showHistory'})} className="blueLink">{state.period}<span>▾</span></button>
            {state.showHistory && <PeriodOptions dispatch={dispatch} state={state}/>}
          </article>
        ):(
          <p className="noHistory">Pay history for <span className="blueLink">All time</span><span>▾</span></p>
        )}
        {(state.history.length > 0 && state.schedule) && <article>
          <span>Pay schedule</span>
          <button onClick={()=>dispatch({type:'showSchedule'})} className="blueLink">{state.schedule}<span>▾</span></button>
          {state.showSchedule && <PaySchedule/>}
        </article>}
      </div>
    )
  }
export default HistoryFilter
