import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import FPSHeader from '../FPSHeader'
import FPSContent from '../FPSContent'
import Tabs,{ Tab } from '../../../ui/Structure/Tabs'
import './FPS.css'

const FPS = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const handleCloseClick = ()=>{
    dispatch({type:'ANIMATE_Drawer'})
    setTimeout(()=>dispatch({type:'CLOSE_Drawer'}),800)
  }

  return(
    <main className="FPS">
      <FPSHeader handleCloseClick={handleCloseClick}/>
      <Tabs current="0">
        <Tab title="Payroll">
          <div className="fpsImg">
            <FPSContent/>
          </div>
        </Tab>
        <Tab title="Activity"><h3>Activity</h3></Tab>
        <Tab title="Review"><h3>Review</h3></Tab>
      </Tabs>
      <button onClick={handleCloseClick} className="done">Submit</button>
    </main>
  )
}
export default FPS
