import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './AddBonus.css'

const AddBonus = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { addPay } = state
  return(
    <header className="AddBonus">
      <h4>{addPay ? 'Pay types ':'Deductions '}ready to add</h4>
      <div>
        <button className="close">
          <span className="visuallyHidden">Add Bonus</span>
          <i className="hi hi-create-1"/>
        </button>
        <button className="close" onClick={()=>dispatch({type:'BACK_TO_BONUS_FIELDS'})}>
          <span className="visuallyHidden">Back to bonus fields</span>
          <i className="hi hi-close"/>
        </button>
      </div>
    </header>
  )
}
export default AddBonus
