import React,{useState,useEffect,useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import ReviewFieldItem from '../ReviewFieldItem'
import Thousands from '../../../Utils/Thousands'
import './ReviewDrawerFields.css'

const ReviewDrawerFields = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees,percent,reviewNetPay } = state
  const emp = employees[currentOperation]
  const uniqueKey = key => currentOperation+key.split(' ').join('').toLowerCase()
  const { operations } = emp
  const opx = addition => {
    if (addition) {
      return Object.keys(operations).filter(key=>operations[key].addition)
    } else {
      return Object.keys(operations).filter(key=>!operations[key].addition)
    }
  }
  const NIEE = ()=> (reviewNetPay * (percent/3*2)/100)
  const PAYE = ()=> (reviewNetPay * (percent/3)/100)
  const [ plusNMinus,setPlusNMinus ] = useState({earnings:null,deducts:null})
  const getIt = ()=>{
    const payMinDeducts = Object.keys(employees[currentOperation].operations).reduce((sum,opx)=>{
      const amount = employees[currentOperation].operations[opx].amount
      const operator = employees[currentOperation].operations[opx].addition
      if (operator) return sum + amount
      return sum - amount
    },0)

    const earnings = Object.keys(employees[currentOperation].operations).reduce((sum,opx)=>{
      const amount = employees[currentOperation].operations[opx].amount
      const operator = employees[currentOperation].operations[opx].addition
      if (operator) return sum + amount
      return sum
    },0)
    const deducts = Object.keys(employees[currentOperation].operations).reduce((sum,opx)=>{
      const amount = employees[currentOperation].operations[opx].amount
      const operator = employees[currentOperation].operations[opx].addition
      if (!operator) return sum + amount
      return sum
    },0)
    setPlusNMinus({earnings,deducts})
    dispatch({type:'REVIEW_NET_PAY',payload:payMinDeducts-(payMinDeducts*percent/100)})
  }
  useEffect(()=>{
    getIt()
  },[])
  return(
    <main className="ReviewDrawerFields">
      <section>
        <h4>Earnings</h4>
        {opx(true).filter(key=>operations[key].isActive).map(key=><ReviewFieldItem  operation={key} id={uniqueKey(key)} key={uniqueKey(key)}/>)}
        <main className="ReviewFieldItem">
          <label htmlFor="total"><strong>Gross Pay</strong></label>
          <div>
            <strong>
              <span className="currency">£</span>
              {plusNMinus.earnings&&<span>{Thousands(plusNMinus.earnings.toFixed(2))}</span>}
            </strong>
          </div>
        </main>

        <h4>Deductions</h4>
        {opx(false).filter(key=>operations[key].isActive).length > 0 &&(
          <>{opx(false).filter(key=>operations[key].isActive).map(key=><ReviewFieldItem  operation={key} id={uniqueKey(key)} key={uniqueKey(key)}/>)}</>
        )}
      <main className="ReviewFieldItem">
        <label htmlFor="niee">NI EE</label>
        <div>
          <span className="currency">£</span>
          <span>{Thousands((NIEE()).toFixed(2))}</span>
        </div>
      </main>
      <main className="ReviewFieldItem">
        <label htmlFor="paye">PAYE</label>
        <div>
          <span className="currency">£</span>
          <span>{Thousands((PAYE()).toFixed(2))}</span>
        </div>
      </main>
      <main className="ReviewFieldItem">
        <label htmlFor="total"><strong>Total</strong></label>
        <div>
          <strong>
            <span className="currency">£</span>
            {plusNMinus.deducts?<span>{Thousands((plusNMinus.deducts+NIEE()+PAYE()).toFixed(2))}</span>:<span>{Thousands((NIEE()+PAYE()).toFixed(2))}</span>}
          </strong>
        </div>
      </main>
      </section>
    </main>
  )
}
export default ReviewDrawerFields
