import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import Switch from '../../../ui/Switch/'
import './BonusItem.css'

const BonusItem = ({bonus})=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees,addPay,bonusTypes,deductionTypes } = state
  const hasIt = employees[currentOperation].operations.hasOwnProperty(bonus)
  const isActive = employees[currentOperation].operations[bonus] ? employees[currentOperation].operations[bonus].isActive : false
  const bonusName = ()=> {
    if (!isActive) return bonus
    return employees[currentOperation].operations[bonus].displayName || bonus
  }
  const handleEditClick = ()=>{
    if (hasIt) dispatch({type:'EDIT_BONUS_NAME',payload:bonus})
  }
  return(
    <article className={`BonusItem ${bonus.split(' ').join('').toLowerCase()}`}>
      <div onClick={handleEditClick}>
        <h4>{bonusName()}</h4>
        <p>{addPay?bonusTypes[bonus]:deductionTypes[bonus]}</p>
      </div>
      <Switch
        handleSwitchChange={()=>dispatch({type:'USE_BONUS',payload:{currentOperation,bonus}})}
        checked={hasIt&&isActive}
        id={currentOperation+bonus.split(' ').join('').toLowerCase()}/>
    </article>
  )
}
export default BonusItem
