import React from 'react'
import Radio from '../Radio'
const RadioGroup = ({group,label,selected,handleRadioClick})=>{
  return (
     <section className="RadioGroup">
      <label>{label}</label>
      {group.map(radio=><Radio key={radio.name} name={radio.name} id={radio.name.split(' ').join('')} handleRadioClick={handleRadioClick} checked={radio.name === selected}>{radio.label}</Radio>)}
     </section>
  )
}

export default RadioGroup
