import React from 'react'
import Tile from '../../ui/Tile/Tile'
import MigrationWorked from './Dashboard/MigrationWorked'
import NxtPay from './Dashboard/NxtPay'
import HMRCReports from './Dashboard/HMRCReports'
import PayrollCost from './Dashboard/PayrollCost'
import Tips from './Dashboard/Tips'
import History from './Dashboard/History'
import './Payroll.css'

const Payroll =()=>{
  return (
        <main className="MigrationWorked">
          <MigrationWorked/>
          <section className="payrollHomeGrid">
            <Tile title="Next payroll"><NxtPay/></Tile>
            <Tile cssClass="HmrcReports" title="HMRC reports"><HMRCReports/></Tile>
            <Tile cssClass="totalCost" title="Total payroll cost"><PayrollCost/></Tile>
            <Tile cssClass="Tips" title="Tips"><Tips/></Tile>
            <History/>
          </section>
        </main>
  )
}
export default Payroll
