import React,{useContext} from 'react'
import SelfAssessmentDispatchContext from '../../context/SelfAssessment/SelfAssessmentDispatchContext'
import './OpenSA.css'
const OpenSA = ()=>{
    const dispatch = useContext(SelfAssessmentDispatchContext)
  return (
    <main className="OpenSA">
      <button className="close" onClick={()=>dispatch({type:'CLOSE_SA_MODAL'})}>
        <span className="visuallyHidden">Close</span>
        <i className="hi hi-close"/>
      </button>
      <h1>This feature works best for these businesses</h1>
      <h2>Sole traders who:</h2>
      <ul>
        <li><span>Use cash or accrual accounting</span></li>
        <li><span>Need to submit a Self Assessment for the tax year 6 April 2019 to 5 April 2020</span></li>
        <li><span>Have earned over £85,000 in that tax year</span></li>
        <li><span>Don't have any disallowable expenses</span></li>
      </ul>
      <p>Sounds like you? Then we’re good to go.</p>
      <div className="btnCtn">
        <span className="blueLink">Learn more about Self Assesment estimates </span>
        <div>
          <button onClick={()=>dispatch({type:'CLOSE_SA_MODAL'})} className="btn ghost"><span>Cancel</span></button>
          <button onClick={()=>dispatch({type:'OPEN_SA_TROWSER'})} className="btn"><span>Got it</span></button>
        </div>
      </div>
      <div></div>
    </main>
  )
}
export default OpenSA
