import React,{ useReducer,useEffect } from 'react'
import Tabs,{ Tab } from '../../ui/Structure/Tabs'
import Modal from '../../ui/Popups/Modal'
import Trowser from '../../ui/Popups/Trowser'
import VAT from '../VAT'
import CIS from '../CIS'
import OpenSA from '../OpenSA'
import SelfAssessmentHome from '../SelfAssessmentHome'
import SelfAssessmentCtn from '../SelfAssessmentCtn'
import SelfAssessmentStateContext from '../../context/SelfAssessment/SelfAssessmentStateContext'
import SelfAssessmentDispatchContext from '../../context/SelfAssessment/SelfAssessmentDispatchContext'
import selfAssessmentReducer, { initialSelfAssessmentState } from '../../reducers/selfAssessmentReducer'
import './taxes.css'

const Taxes = ({tab})=>{
  const [state, dispatch] = useReducer(selfAssessmentReducer, initialSelfAssessmentState)
  const { viewEstimate,viewModal } = state
  useEffect(()=>{
    if(viewEstimate||viewModal){
      document.getElementById('qboWrapper').style.overflow = 'hidden'
    } else {
      document.getElementById('qboWrapper').style.overflow = 'inherit'
    }
  },[viewEstimate,viewModal])
  return (
    <SelfAssessmentDispatchContext.Provider value={dispatch}>
      <SelfAssessmentStateContext.Provider value={state}>
        <main className="Taxes">
          <Tabs current={tab}>
            <Tab title="VAT"><VAT/></Tab>
            <Tab title="CIS"><CIS/></Tab>
            <Tab title="Self Assessment"><SelfAssessmentHome/></Tab>
          </Tabs>
          <Trowser open={viewEstimate}>
            <SelfAssessmentCtn/>
          </Trowser>
          <Modal open={viewModal}>
            <OpenSA/>
          </Modal>
        </main>
      </SelfAssessmentStateContext.Provider>
    </SelfAssessmentDispatchContext.Provider>

  )
}
export default Taxes
