let initialPayrollState = {
  history:[],
  noItm: false,
  trowserOpen: false,
  useData: null,
  software: null,
  payrollFile: null,
  current:0,
  delta:4,
  paySchedule:'Choose one',
  payDays:'1 to 31 March',
  length:0,
  scheduleName:'',
  pensionPlan:null,
  pensionType:null,
  billType:null,
  width: 0,
  base:'/self-assessment/',
  step4:false,
  payDate: new Date(),
  heights:[300]
}

const payrollReducer = (state, action) => {
  switch (action.type) {

    case 'SELECT_PENSION_PLAN':
      let SELECT_PENSION_PLAN = {...state}
      SELECT_PENSION_PLAN.pensionPlan = action.payload
    return SELECT_PENSION_PLAN;

    case 'SELECT_BILL_TYPE':
      let SELECT_BILL_TYPE = {...state}
      SELECT_BILL_TYPE.billType = action.payload
    return SELECT_BILL_TYPE;

    case 'SELECT_PENSION_TYPE':
      let SELECT_PENSION_TYPE = {...state}
      SELECT_PENSION_TYPE.pensionType = action.payload
    return SELECT_PENSION_TYPE;

    case 'SET_HEIGHT':
      let SET_HEIGHT = {...state}
      SET_HEIGHT.heights[action.payload.step] = action.payload.height
    return SET_HEIGHT;

    case 'SET_FOUR':
      let SET_FOUR = {...state}
      SET_FOUR.step4 = true
    return SET_FOUR;

    case 'SCHEDULE_NAME':
      let SCHEDULE_NAME = {...state}
      SCHEDULE_NAME.scheduleName = action.payload
    return SCHEDULE_NAME;

    case 'SET_PAY_DATE':
      let SET_PAY_DATE = {...state}
      SET_PAY_DATE.payDate = action.payload
    return SET_PAY_DATE;

    case 'PAY_DAYS':
    let PAY_DAYS = {...state}
    PAY_DAYS.payDays = action.payload
    return PAY_DAYS;

    case 'PAY_SCHEDULE':
    let PAY_SCHEDULE = {...state}
    PAY_SCHEDULE.paySchedule = action.payload
    return PAY_SCHEDULE;

    case 'DELETE_DROP_FILE':
      let DELETE_DROP_FILE = {...state}
      DELETE_DROP_FILE.payrollFile = null
    return DELETE_DROP_FILE;

    case 'SET_DROP_FILE':
      let SET_DROP_FILE = {...state}
      SET_DROP_FILE.payrollFile = action.payload
    return SET_DROP_FILE;

    case 'SELECT_SOFTWARE':
      let SELECT_SOFTWARE = {...state}
      let software = action.payload
      if(action.payload === SELECT_SOFTWARE.software) software = null
      SELECT_SOFTWARE.software = software
    return SELECT_SOFTWARE;

    case 'SELECT_USEDATA':
      let SELECT_USEDATA = {...state}
      let useData = action.payload ? 'cloud':'people'
      if((action.payload && SELECT_USEDATA.useData === 'cloud') || (!action.payload && SELECT_USEDATA.useData === 'people')) useData = null
      SELECT_USEDATA.useData = useData
    return SELECT_USEDATA;

    case 'openTrowser':
      let openTrowser = {...state}
      openTrowser.current = 0
      openTrowser.trowserOpen = true
      return openTrowser;

    case 'closeTrowser':
      let closeTrowser = {...state}
      closeTrowser.trowserOpen = false
      return closeTrowser;

      case 'setDimensions':
        let setDimensions = {...state}
        let a = action.payload
        setDimensions.length = a.length
        setDimensions.width = a.width
        setDimensions.heights = a.heights
        return setDimensions;

      case 'next':
        let next = {...state}
        next.current++
        return next;

      case 'prev':
        let prev = {...state}
        prev.current--
        return prev;

    default: throw new Error('Unexpected action');
  }
}
export default payrollReducer
export { initialPayrollState }
