import React from 'react'
import './TrowserTopbar.css'
const TrowserTopbar =({handleCloseTrowser,title})=>(
  <header className="TrowserTopbar">
    <h2>{title}</h2>
    <button className="close" onClick={handleCloseTrowser}><i className="hi hi-close"></i><span>Close</span></button>
  </header>
)
export default TrowserTopbar
