import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import ImportFile from '../ImportFile'
import DeleteFile from '../DeleteFile'
import Drop from '../../../ui/Drop'
import './BringData.css'
const BringData = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { payrollFile } = state
  return (
    <main className="BringData">
      <h3>Now, let's bring in your payroll data</h3>
      <p>The fine you need will probably be labelled Full Payment Submission</p>
      <p>If you're running both monthly and weekly payroll, select the FPS file with highest nu,ber of employees.You can add the missing info later.</p>
      <Drop cb={file=>dispatch({type:'SET_DROP_FILE',payload:file[0].name})}>
        {payrollFile?<DeleteFile/>:<ImportFile/>}
      </Drop>
      <h4>Need help?</h4>
      <span className="blueLink">Find out how to get the file from your payroll software</span>
    </main>
  )
}
export default BringData
