import React,{useContext} from 'react'
import il_utl_checklist from '../../assets/images/il_utl_checklist.svg'
import SelfAssessment from '../SelfAssessment'
import SelfAssessmentDispatchContext from '../../context/SelfAssessment/SelfAssessmentDispatchContext'
import './SelfAssessmentCtn.css'
const SelfAssessmentCtn = ()=>{
  const dispatch = useContext(SelfAssessmentDispatchContext)
  return (
    <main className="SelfAssessmentCtn">
      <header>
        <h1>Self Assessment for tax year 2019 - 2020</h1>
        <button className="close" onClick={()=>dispatch({type:'CLOSE_SA_TROWSER'})}>
          <span className="visuallyHidden">Close</span>
          <i className="hi hi-close"/>
        </button>
      </header>
      <div className="selfCtn">
        <SelfAssessment/>
      </div>
    </main>
  )
}
export default SelfAssessmentCtn
