import React from 'react'
import PHeader from './PHeader'
import EmployeeList from './EmployeeList'
import AddEmployee from './AddEmployee'
import Note from '../../ui/Note'
import './Payrun.css'
const gov = "https://www.gov.uk/guidance/calculate-how-much-you-can-claim-using-the-coronavirus-job-retention-scheme"
const Payrun = ()=> {
  return (
    <main className="Payrun">
      <header>
        <PHeader/>
        <Note theme="blue"><p>Need help caclulating the wages due to a furloughed worker? Check out HMRC's handy <a rel="noopener noreferrer" target="_blank" href={gov}>calculator</a></p></Note>
      </header>
      <EmployeeList/>
      <p style={{"textAlign":"right","paddingTop":"15px"}}className="lookingAt">Looking to add a Bonus, Payments or Deduction? Find out more about <span className="blueLink">Payments</span> and  <span className="blueLink">Deductions</span></p>
      <AddEmployee/>
    </main>
  )
}
export default Payrun
