import React,{ useContext, useRef } from 'react'
import { Link } from "react-router-dom"
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import TrowserTopbar from '../../../ui/Popups/Trowser/TrowserTopbar'
import Progress from '../../../ui/Progress'
import UseData from '../UseData'
import Software from '../Software'
import BringData from '../BringData'
import NicelyPlayed from '../NicelyPlayed'
import SetupSchedule from '../SetupSchedule'
import SortPension from '../SortPension'
import EmployersBill from '../EmployersBill'
import YourEmployees from '../YourEmployees'
import TileShadow from '../../../ui/TileShadow'
import Slider,{ Step } from '../../../ui/Slider'
import './ImportData.css'
const ImportData =()=>{

  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { current, length,payrollFile,base } = state
  const sliderRef = useRef(null)
  const titles = ["Getting started","Import your data"]
  const getTitle = ()=>{
    if(current <= 2) return titles[0]
    return titles[1]
  }

  const nxt = ()=>{
    let nt
    switch (current) {
      case 1: nt = "Upload data";break;
      case 2: nt = "Upload";break;
      case 3: nt = "Finish company setup";break;
      default: nt = "Next"
    }
    return nt
  }

  return (
    <main className="ImportData">
      <TrowserTopbar
        title={getTitle()}
        handleCloseTrowser={()=>dispatch({type:'closeTrowser'})}/>
        <section className="ImportDataCtn">
          <Progress/>
          <TileShadow>
            <Slider ref={sliderRef}>
              <Step><UseData/></Step>
              <Step><Software/></Step>
              <Step><BringData/></Step>
              <Step><NicelyPlayed/></Step>
              <Step><SetupSchedule/></Step>
              <Step><SortPension/></Step>
              <Step><EmployersBill/></Step>
              <Step><YourEmployees/></Step>
            </Slider>
          </TileShadow>
          <section className="btnCtn">
            <button className="btn ghost" disabled={current === 0} onClick={()=>dispatch({type:'prev'})}><span>Prev</span></button>
            {current !== length - 1&&<button
              className={`btn ${current === length-1 ?'hide':'' }`}
              disabled={(current === length - 1)||(current === 2 && payrollFile === null)}
              onClick={()=>dispatch({type:'next'})}><span>{nxt()}</span></button>}
              {current === (length-1)&&<button onClick={()=>dispatch({type:'closeTrowser'})} className="btn"><Link to={base+'/employees'}><span>Done</span></Link></button>}
          </section>
        </section>
    </main>
  )
}
export default ImportData
