import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import './EditBonus.css'

const EditBonus = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const handleBlueLinkClick = e => {
    if(e.target.classList.contains('blueLink')) dispatch({type:'ADD_BONUS',payload:e.target.classList.contains('add')})
  }
  return(
    <div className="EditBonus" onClick={handleBlueLinkClick}>
      <div><button className="blueLink add">Add/edit a bonus or another payment</button></div>
      <div><button className="blueLink">Add/edit a deduction</button></div>
    </div>
  )
}
export default EditBonus
