import React,{ useContext } from 'react'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import ReviewEmployee from '../ReviewEmployee'
import DraftHeader from '../DraftHeader'
import TotalReview from '../TotalReview'
import './Draft.css'
const Draft = ()=>{
  const state = useContext(PayrollStateContext)
  return(
    <main className="Draft">
      <DraftHeader/>
      <main className="ReviewList">
        <section>
          {Object.keys(state.employees).filter(k=>state.employees[k].shouldGetPaid).map(key=><ReviewEmployee key={key} id={key}/>)}
        </section>
        <TotalReview/>
      </main>
    </main>
  )
}
export default Draft
