import React,{useContext} from 'react'
import { Link } from "react-router-dom"
import DatePick from '../../../ui/DatePick'
import BackLink from '../../../ui/BackLink'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './PHeader.css'

const PHeader = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { period,schedule,payDate,base } = state
  return(
    <main className="PHeader">
      <section>
        <BackLink path="/payroll/" txt="Payroll Dashboard"/>
        <h1>Is all employee pay correct?</h1>
        <table className="headTable" cellSpacing="0">
          <thead>
            <tr>
              <th>Pay schedule</th>
              <th>Pay period</th>
              <th>Pay date</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td><span>{schedule}</span></td>
              <td><span>{period}</span></td>
              <td><DatePick selected={payDate} handleDateChange={date=>dispatch({type:'SET_PAY_DATE',payload:date})} /></td>
            </tr>
          </tbody>
        </table>
      </section>
      <aside>
        <button className="btn"><Link to={base+'/employees/draft'}><span>Save and view draft</span></Link></button>
      </aside>
    </main>
  )
}
export default PHeader
