import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './EditSalary.css'

const EditSalary = ({id})=>{
    const dispatch = useContext(PayrollDispatchContext)
    const state = useContext(PayrollStateContext)
    const { employees } = state
    const { shouldGetPaid,operations } = employees[id]
  return(
    <article className="EditSalary">
      <label htmlFor={'salary_'+id}>Salary</label>
      <div>
        <span>£</span>
        <input
          value={operations.Salary.amount}
          type="number"
          id={'salary_'+id}
          onChange={e=>dispatch({type:'UPDATE_OPERATIONS',payload:{id,operation:'Salary',amount:e.target.value}})}
          disabled={!shouldGetPaid}/>
      </div>
    </article>
  )
}
export default EditSalary
