import React,{ useState } from 'react'
import Tile from '../../../../ui/Tile/Tile'
import Shorts from '../../../../ui/Popups/Shorts'
import orange_woman from '../../../../assets/images/illustrations/orange_woman.svg'

const MigrationWorked =()=>{
  const handleCloseMigration = ()=>{
    if (showAgain) {
      setShowShorts(true)
    } else {
      setShowTile(false)
    }
  }
  const handleCloseForever = ()=>{
    setShowAgain(false)
    setShowShorts(false)
    setShowTile(false)
  }

  const [showTile,setShowTile] = useState(true)
  const [showAgain,setShowAgain] = useState(true)
  const [showShorts,setShowShorts] = useState(false)
  return (
    <main className="MigrationWorked">
      {(showShorts && showAgain) && <Shorts>
        <button onClick={()=>setShowShorts(false)} className="close hi hi-close"></button>
        <h2>Close forever?</h2>
        <p>Decide if you want to see this message again next time you sign in, or for it to be gone forever.</p>
        <div className="btnCtn">
          <button onClick={()=>setShowShorts(false)} className="btn ghost"><span>Show again</span></button>
          <button onClick={handleCloseForever} className="btn"><span>Close forever</span></button>
        </div>
      </Shorts>}
      <div className="linkCtn">
        <span className="blueLink">Need help with Payroll?</span>
      </div>
      {showTile && <Tile cssClass="dismissMigration">
        <button onClick={handleCloseMigration} className="close closeMigration hi hi-close"></button>
        <div>
          <h1>Migration worked a treat!</h1>
          <p>You’ve successfully moved to QuickBooks Online Standard Payroll. <br/>Get started using our tutorial videos and handy articles.</p>
          <button className="btn ghost">
            <a
              rel="noopener noreferrer"
              href="https://quickbooks.intuit.com/uk/payroll/standard-starter-kit/"
              target="_blank">
              <span>Get started</span>
            </a>
          </button>
        </div>
        <div className="illuCtn"><img src={orange_woman} alt="Successful migration"/></div>
      </Tile>}
    </main>
  )
}
export default MigrationWorked
