import React,{ useContext } from 'react'
import './HistoryList.css'
import PayrollDispatchContext from '../../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../../context/Payroll/PayrollStateContext'

const HistoryItem =({itm})=>(
  <li className={!itm?'noData':''}>
    <table>
      <thead>
        <tr>
          <th>Pay date</th>
          <th>Pay period</th>
          <th>Tax period</th>
          {itm&&<th>Pay schedule</th>}
          <th>Total cost</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{itm.payDate}</td>
          <td>{itm.payPeriod}</td>
          <td>{itm.taxPeriod}</td>
          {itm&&<td>{itm.paySchedule ? 'Weekly' : 'Monthly'}</td>}
          <td>{itm.totalCost}</td>
        </tr>
      </tbody>
    </table>
  </li>
)
const HistoryList =()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  return (
    <div>
      {state.history.length ? (
        <div>
          <ul className="HistoryList">
            {state.history.map((itm,idx)=>{
              switch (state.schedule) {
                case 'Monthly': if (!itm.paySchedule) return <HistoryItem schedule={state.schedule} itm={itm} key={idx}/>;break;
                case 'Weekly': if (itm.paySchedule) return <HistoryItem schedule={state.schedule} itm={itm} key={idx}/>;break;
                default: return <HistoryItem itm={itm} key={idx}/>
              }return null
            }).splice(0,state.max)}
          </ul>
          <p className="blueLink showMore" onClick={()=>dispatch({type:'showMore'})}>Show more</p>
        </div>
      ):(<ul className="HistoryList"><HistoryItem itm={state.noItm}/></ul>)
    }
    </div>
  )
}
export default HistoryList
