import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import './ImportFile.css'
const ImportFile = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  return (
    <div className="ImportFile">
      <i className="hi hi-upload"/>
      <p>Drag and drop a file here or <label htmlFor="browse" className="blueLink">browse to upload</label></p>
      <input type="file" id="browse" onChange={e=>dispatch({type:'SET_DROP_FILE',payload:e.target.files[0].name})} className="visuallyHidden"/>
    </div>
  )
}
export default ImportFile
