import React,{ useReducer,useEffect,useState } from 'react'
import './bolt_fonts.css'
import './assets/iconfont/harmonyicons.css'
import './App.css'
import Topbar from './ui/Structure/Topbar'
import Sidenav from './ui/Structure/Sidenav'
import Drawer from './ui/Structure/Drawer'
import Pile from './ui/Pile'
import Dashboard from './components/Dashboard'
import Employees from './components/Employees'
import Payrun from './components/Payrun'
import Landing from './components/Landing'
import Taxes from './components/Taxes'
import Draft from './components/Payrun/Draft'
import Confirm from './components/Payrun/Confirm'
import OperationsDetail from './components/Payrun/OperationsDetail'
import ReviewDrawer from './components/Payrun/ReviewDrawer'
import Payslips from './components/Payrun/Payslips'
import FPS from './components/Payrun/FPS'
import PayrollStateContext from './context/Payroll/PayrollStateContext'
import PayrollDispatchContext from './context/Payroll/PayrollDispatchContext'
import payrollReducer, { initialPayrollState } from './reducers/payrollReducer'
import { BrowserRouter as Router, Route } from "react-router-dom"

const App = ()=>{
  const [state, dispatch] = useReducer(payrollReducer, initialPayrollState)
  const { DrawerOpen,base,Drawer,payrollDeleted,payrollPage } = state
  const [ taxTab,setTaxTab] = useState(0)
  useEffect(()=>{
    if (window.innerHeight < document.getElementById('qboWrapper').clientHeight) {
      let system = 'pc'
      if (navigator.userAgent.indexOf('Mac OS X') !== -1) system = 'mac'
      DrawerOpen?document.body.classList.add('noOverflow'+system):document.body.classList.remove('noOverflow'+system)
    }
    const tab = window.location.href.split('/self-assessment/')[1]
    switch (tab) {
      case '':setTaxTab(2);break;
      case 'vat':setTaxTab(0);break;
      case 'cis':setTaxTab(1);break;
      case 'selfassessment':setTaxTab(2);break;
      default:setTaxTab(0)
    }
  },[DrawerOpen,window.location.href])

  return (
    <PayrollDispatchContext.Provider value={dispatch}>
      <PayrollStateContext.Provider value={state}>
        <Router>
          <Route exact path={base+'/'}><Landing/></Route>
          <div id="qboWrapper" className={`qboWrapper ${payrollPage ? 'payrollPage':''}`}>
            <Topbar/>
            <section className="qboContentWrapper">
              <Sidenav/>
              <main className="routeView">
                <Route exact path={base+'/dashboard'}><Dashboard/></Route>
                <Route exact path={base+'/payroll'}><Employees/></Route>
                <Route exact path={base+'/employees'}><Payrun/></Route>
                <Route exact path={[base+'/taxes',base+'/vat',base+'/cis',base+'/selfassessment']}><Taxes tab={taxTab}/></Route>
                <Route exact path={base+'/employees/draft'}><Draft/></Route>
                <Route exact path={base+'/employees/confirm'}><Confirm/></Route>
              </main>
            </section>
            {(DrawerOpen&&Drawer==='edit')&&(
              <Drawer>
                <OperationsDetail/>
              </Drawer>
            )}
            {(DrawerOpen&&Drawer==='review')&&(
              <Drawer>
                <ReviewDrawer/>
              </Drawer>
            )}
            {(DrawerOpen&&Drawer==='payslips')&&(
              <Drawer>
                <Payslips/>
              </Drawer>
            )}
            {(DrawerOpen&&Drawer==='FPS')&&(
              <Drawer>
                <FPS/>
              </Drawer>
            )}
            {payrollDeleted&&<Pile message="Payroll successfully deleted"/>}
        </div>
      </Router>
    </PayrollStateContext.Provider>
  </PayrollDispatchContext.Provider>
  )
}

export default App
