import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import Initials from '../Initials'
import Thousands from '../../../Utils/Thousands'
import './ReviewDrawerHeader.css'

const ReviewDrawerHeader = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees,period,reviewNetPay } = state
  const handleButton = ()=>{
    dispatch({type:'ANIMATE_Drawer'})
    setTimeout(()=>dispatch({type:'CLOSE_Drawer'}),800)
  }

  return(
    <header className="ReviewDrawerHeader">
      <article>
        <span>Employee Payslip</span>
        <div>
          <button className="close" onClick={handleButton}>
            <i className="hi hi-close"/>
            <span className="visuallyHidden">close</span>
          </button>
        </div>
      </article>
      <article>
        <Initials name={employees[currentOperation].name}/>
        <h5>{employees[currentOperation].name}</h5>
      </article>
      <article>
        <div>
          <label>Pay Period</label>
          <span>{period}</span>
        </div>
        <div>
          <label>Net Pay</label>
          <span>£{Thousands(reviewNetPay.toFixed(2))}</span>
        </div>
      </article>
    </header>
  )
}
export default ReviewDrawerHeader
