import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import people from '../../../assets/images/peopleadd.png'
import cloud from '../../../assets/images/cloud.png'
import './UseData.css'
const UseData = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { useData } = state
  const handleUseDataClick = e=>{
    if(e.target.classList.contains('tileCtn')) return
    dispatch({type:'SELECT_USEDATA',payload:e.target.classList.contains('cloud')})
  }
  return (
    <main className="UseData">
      <h3>Would you like to use your data from another payroll software?</h3>
      <h4>To save time, upload a file from previous software to set up your employees information</h4>
      <section className="tileCtn" onClick={e=>handleUseDataClick(e)}>
        <div className={`cloud ${useData === 'cloud' ? 'selected':''}`}>
          {useData === 'cloud' ? <i className="cloud hi hi-circle-check"/>:<img className="cloud" src={cloud} alt="cloud"/>}
          <span className="cloud">Yes, import my data</span>
        </div>
        <div className={`people ${useData === 'people' ? 'selected':''}`}>
          {useData === 'people' ? <i className="hi hi-circle-check"/>:<img className="people" src={people} alt="people"/>}
          <span>No, make a fresh start</span>
        </div>
      </section>
    </main>
  )
}
export default UseData
