import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './DetailFieldItem.css'

const DetailFieldItem = ({operation,id})=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees } = state
  const emp = employees[currentOperation]
  const { operations } = emp
  const opx = operations[operation]
  return(
    <main className={`DetailFieldItem ${operation === 'Overtime'?'overtime':''}`}>
      <label>{operation}</label>
      <div>
        {operation !== 'Overtime'&&<span className="currency">£</span>}
        <input
          type="number"
          value={opx.amount}
          onChange={e=>dispatch({type:'UPDATE_OPERATIONS',payload:{
              id:currentOperation,
              operation,
              amount:e.target.value
            }})}/>
        {operation === 'Overtime'&&<span className="hrs">hrs</span>}
      </div>
    </main>
  )
}
export default DetailFieldItem
