import React,{useState} from 'react'
import QBSelect from '../../../ui/QBSelect'
import Switch from '../../../ui/Switch'
import './FPSContent.css'

const FPSContent = ({ handleCloseClick }) =>{
  const [ID,setID] = useState('')
  const [pass,setPass] = useState('')
  const [paye,setPaye] = useState('')
  const [office,setOffice] = useState('')
  const [cred,setCred] = useState(true)
  const [fps,setFps] = useState(false)
  const options = [
    'A Notational: Payment to expat by third party or overseas employer',
    'B Notational: Employment-related security',
    'C Notational: Other',
    'D Payment subject to Class 1 NICs, but P11D/P9D for tax',
    'F No P11 requirement or Impractical to report work on the day',
    'G Reasonable excuse',
    'H Correction to earlier submission'
  ]

  return (
    <main className="FPSContent">
      <p>Every time you pay employees, you must submit an FPS to HMRC that includes all payments made to employees, along with any deductions. If an employee leaves the company, your FPS should include their leaving date.</p>
      <h4>Why am I required to send this?</h4>
      <p>You are required to submit this filing because HMRC requires this when you pay employees in tax period recently. For more information on why this is required, visit the <a href="https://www.gov.uk/government/organisations/hm-revenue-customs" className="blueLink">HMRC information page</a>.</p>
      <article>
        <h3>Let's verify your HMRC info</h3>
        We'll use this info to submit your filing to HMRC.
        <div>
          <label htmlFor="gateID">Gateway user ID *</label>
          <input type="text" id="gateID" value={ID} onChange={e=>setID(e.target.value)}/>
        </div>
        <div>
          <label htmlFor="pass">Gateway password *</label>
          <input type="text" id="pass" value={pass} onChange={e=>setPass(e.target.value)}/>
        </div>
        <span className="blueLink">Forgot password?</span>
        <div>
          <label htmlFor="cred">Save gateway credentials</label>
          <Switch
            checked={cred}
            id="cred"
            handleSwitchChange={()=>setCred(!cred)}/>
        </div>
        <small>Be sure you've activated PAYE for the Government Gateway.</small>
        <span className="blueLink">Why save Gateway credentials?</span>
        <div>
          <label htmlFor="fps">Submit FPS automatically to HMRC with payroll</label>
          <Switch
            checked={fps}
            id="fps"
            handleSwitchChange={()=>setFps(!fps)}/>
        </div>
        <small>We'll submit your data straight to HMRC each time you run payroll.</small>
      </article>
      <article>
        <div>
          <label htmlFor="paye">PAYE reference *</label>
          <input placeholder="123/AA45678" id="paye" type="text" value={paye} onChange={e=>setPaye(e.target.value)}/>
        </div>
        <div>
          <label htmlFor="office">Accounts Office refrence *</label>
          <input placeholder="123PQ7654321X" id="office" type="text" value={office} onChange={e=>setOffice(e.target.value)}/>
        </div>
        <div>
          <label htmlFor="reason">Reason for filing late *</label>
          <QBSelect label="Select one" id="reason" options={options} top={true}/>
        </div>
      </article>
    </main>
  )
}
export default FPSContent
