import React from 'react'
import vatImg from '../../assets/images/vat.png'
import './VAT.css'
const VAT = ()=>(
  <main className="VAT">
    <div><img src={vatImg} height="124" alt="vat"/></div>
    <h1>Let's set you up tp collect and track VAT</h1>
    <h2>Add VAT to your invoices and receipts, plus track how much you owe.</h2>
    <div><button className="btn"><span>Set up VAT</span></button></div>
  </main>
)
export default VAT
