import React,{ useContext } from 'react'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import Employee from '../Employee'
import './EmployeeList.css'

const EmployeeList = ()=>{
    const state = useContext(PayrollStateContext)
  return(
    <section className="EmployeeList">
      {Object.keys(state.employees).map(key=><Employee key={key} id={key}/>)}
    </section>
  )
}
export default EmployeeList
