import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './Software.css'
const Software = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { software } = state
  const softwares = [
    'QuickBooks*Desktop',
    'HMRC Basic*Tools',
    'Sage','Xero',
    'Bright Pay',
    'Kashflow'
  ]
  const handleSoftwareClick = e=>{
    if(e.target.classList.contains('tileCtn')) return
    dispatch({type:'SELECT_SOFTWARE',payload:e.target.classList[0]})
  }
  return (
    <main className="Software">
      <h3>What software did you use to pay your employees?</h3>
      <h4>We'll import your tax details and employee info</h4>
      <section className="tileCtn" onClick={e=>handleSoftwareClick(e)}>
        {softwares.map(opt=>{
          const key = opt.split(' ').join('').toLowerCase()
          return (
            <div key={key} className={`${key} ${software === key ? 'selected':''}`}>
              {software === key&& <i className="cloud hi hi-circle-check"/>}
              {opt.split('*').map(soft=><span key={soft} className={key}>{soft}</span>)}
            </div>
          )
        })}
      </section>
      <span className="blueLink">My software isn't on the list</span>
    </main>
  )
}
export default Software
