import React,{useState,useEffect} from 'react'
import './Modal.css'
const Modal = ({open,children}) => {
  const [ gone,setGone ] = useState(true)
  const [ fade,setFade ] = useState(false)
  useEffect(()=>{
    if (open) {
      setGone(true)
      setFade(true)
    } else {
      setFade(false)
      setTimeout(()=>{
        setGone(false)
      },500)
    }
  },[open])
  return <div className={`Modal ${fade ? 'open':''} ${gone ? 'gone':''}`}><main>{children}</main></div>
}

export default Modal
