import React from 'react'
import './Switch.css'

const Switch = ({checked,id,handleSwitchChange})=>(
  <label className={`Switch ${checked?'checked':''}`} htmlFor={id}>
    <input className="visuallyHidden" onChange={()=>1} onClick={handleSwitchChange} type="radio" id={id} checked={checked}/>
  </label>
)
export default Switch
