import React,{ useContext } from 'react'
import './Progress.css'
import PayrollStateContext from '../../context/Payroll/PayrollStateContext'

const Progress = ()=>{
  const { current,length,delta } = useContext(PayrollStateContext)
  return (
    <main className={`progressBar ${(current>=delta && current<length-1) ?'show':''}`}>
      {current<= 6&&<span className="stepOf">{(current-delta)+1 + ' of ' + ((length-1)-delta)}</span>}
      <section>
        <div className="progress" style={{'width':Math.round((((current-delta)+1) / ((length-1)-delta)) * 100) + '%'}}></div>
      </section>
    </main>
  )
}
export default Progress
