import React from 'react'
import SelfAssessmentData from '../../../Data/SelfAssessment/SelfAssessment.json'
import './EstimatedIncome.css'

const EstimatedIncome = ()=>{
  const biz = SelfAssessmentData["business income"]
  const exp = SelfAssessmentData["business expenses"]
  const totalBizDis = Object.keys(biz).reduce((sum,itm)=>biz[itm]["disallowable"]+sum,0)
  const totalBizTot = Object.keys(exp).reduce((sum,itm)=>exp[itm]["total"]+sum,0)
  const totalExp = Object.keys(exp).reduce((sum,itm)=>exp[itm]["disallowable"]+sum,0)
  const ni = SelfAssessmentData["national insurance contributions"]
  const totalNi = Object.keys(ni).reduce((sum,itm)=>ni[itm]+sum,0)
  const taxIncome = (totalBizDis - (totalExp + totalBizTot)) - totalNi
  return (
    <main className="EstimatedIncome">
      <article>
        <h2>Estimated income  tax</h2>
        <strong>£{taxIncome}</strong>
        <span>Tax year 2019 - 2020</span>
        <span>Submit by 31 Jan 2020</span>
      </article>
    </main>
  )
}
export default EstimatedIncome
