import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './BonusSearch.css'

const BonusSearch = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { addPay,searchValue } = state
  return(
    <main className="BonusSearch">
      <input
        value={searchValue}
        onChange={e=>dispatch({type:'SUBMIT_BONUS_SEARCH',payload:e.target.value})}
        type="search"
        placeholder={`Search ${addPay ?'pay types':'deductions'}`}/>
      <i className="hi hi-search"/>
    </main>
  )
}
export default BonusSearch
