import React,{useContext,useState} from 'react'
import { useHistory } from 'react-router-dom'
import PayrollStateContext from '../../context/Payroll/PayrollStateContext'
import './Landing.css'
const QBOPass = process.env.REACT_APP_QBO_PASS
const Landing = () => {
  const state = useContext(PayrollStateContext)
  const { base } = state
  const [password,setPassword] = useState('')
  const [beams,setBeams] = useState(0)
  const [stepExit,setStepExit] = useState(null)
  const moveBeams = e=>setBeams(e.clientX)
  const history = useHistory()
  const navigateAway =()=> {
    setStepExit(true)
    setTimeout(()=>{
      history.push(base+'/taxes')
    },600)
  }
  return (
    <div
      tabIndex="0"
      onMouseMove={moveBeams}
      className={`landing ${stepExit ? 'exit' : ''}`}>
      <section className="centerWidth">
        <h1>
          <span>
            <span>Improving</span>
          </span>
          <span>
            <span>Self Assessment</span>
          </span>
        </h1>
        <h2>Thank you for taking the time to help us<br/> improve your experience on QuickBooks</h2>
        <div className="btnCtn">
          {password === QBOPass ?
            <button className="btn dull_white" onClick={navigateAway}>Let's Start</button> :
            <input type="password" onChange={e=>setPassword(e.target.value)} placeholder="Password" value={password}/>
          }
        </div>
      </section>
      <div className="beams">
        <i><i style={{transform:'translateY( ' + (beams * -.01 )+ 'px)'}}></i></i>
        <i><i style={{transform:'translateY( ' + (beams * -.03 )+ 'px)'}}></i></i>
        <i><i style={{transform:'translateY( ' + (beams * -.06 )+ 'px)'}}></i></i>
      </div>
    </div>
  )
}
export default Landing
