import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import EmployeeName from '../EmployeeName'
import EditSalary from '../EditSalary'
import Thousands from '../../../Utils/Thousands'
import Switch from '../../../ui/Switch/'
import './Employee.css'

const Employee = ({id})=>{
    const dispatch = useContext(PayrollDispatchContext)
    const state = useContext(PayrollStateContext)
    const { employees } = state
    const getAllDeductions = Object.keys(employees[id].operations).reduce((sum,itm)=>{
      if (!employees[id].operations[itm].addition) return sum + employees[id].operations[itm].amount
      return sum
    },0)
    const getAllPayments = Object.keys(employees[id].operations).reduce((sum,itm)=>{
      if (employees[id].operations[itm].addition) return sum + employees[id].operations[itm].amount
      return sum
    },0)
    const { shouldGetPaid } = employees[id]
    const handleEditClick = ()=> {
      dispatch({type:'EDIT_OPERATIONS',payload:{id,Drawer:'edit'}})
      setTimeout(()=>dispatch({type:'ANIMATE_Drawer'}),5)
    }
  return(
    <section className="Employee">
      <div className="controls">
        <Switch
          handleSwitchChange={()=>dispatch({type:'TOGGLE_SHOULD_GET_PAID',payload:id})}
          checked={shouldGetPaid}
          id={id}/>
        <section className={`enableEdit ${!shouldGetPaid ? 'disabled':''}`}>
          <div>
            <EmployeeName id={id}/>
            <EditSalary id={id}/>
          </div>
          <div className="calculate">
            <article>
              <label>Additional deductions</label>
              <span>£{Thousands(getAllDeductions.toFixed(2))}</span>
            </article>
            <article>
              <label>Additional payments</label>
              <span>£{Thousands(getAllPayments-employees[id].operations.Salary.amount.toFixed(2))}</span>
            </article>
            <article>
              <label>Gross pay</label>
              <strong>£{Thousands(getAllPayments-getAllDeductions.toFixed(2))}</strong>
            </article>
          </div>
        </section>
      </div>
      <div><button onClick={handleEditClick} className="EditPayrun"><span className="blueLink">Edit</span><i className="hi hi-chevron-right"/></button></div>
    </section>
  )
}
export default Employee
