import React,{ useRef,useEffect,useState } from 'react'
import SelfAssessmentData from '../../../Data/SelfAssessment/SelfAssessment.json'
import Thousands from '../../../Utils/Thousands'
const BusinessIncome = ()=>{
  const [ collapsed,setCollapsed ] = useState(false)
  const box = 15
  const biz = SelfAssessmentData["business income"]
  const total = Object.keys(biz).reduce((sum,itm)=>biz[itm]["disallowable"]+sum,0)
  const [ height,setHeight ] = useState(null)
  const collapsedtHeight = 40
  const vRef = useRef(null)
  useEffect(()=>{
    setHeight(vRef.current.clientHeight - 30)
  },[])
  return (
    <main
      style={{"height":collapsed?collapsedtHeight:height}}
      ref={vRef}
      className={`SATable BusinessIncome ${collapsed ? 'collapsed':''}`}>
      <h2 onClick={()=>setCollapsed(!collapsed)}>
        <i className="hi hi-chevron-down"/>
        <span>Business income</span>
      </h2>
      <table cellSpacing="0">
        <tbody>
          {Object.keys(biz).map((key,i)=>(
            <tr key={key}>
              <td>{key}</td>
              <td></td>
              <td></td>
              <td>Box {box+i}</td>
              <td>£{Thousands(biz[key]["disallowable"])}</td>
            </tr>
          )
        )}
        </tbody>
        <tfoot>
          <tr>
            <td>Total income</td>
            <td></td>
            <td></td>
            <td></td>
            <td>£{total}</td>
          </tr>
        </tfoot>
      </table>
    </main>
  )
}
export default BusinessIncome
