import React,{ useEffect,useState,useContext } from 'react'
import ActionButton from '../../../ui/ActionButton'
import DatePick from '../../../ui/DatePick'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './SetupSchedule.css'
const SetupSchedule = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { paySchedule,payDate,payDays,scheduleName,step4 } = state
  const [ showMore,setShowMore ] = useState(true)
  const title = "Let's setup your employees' pay schedule"
  const options = [
    'Every month',
    'Every week',
    'Every day',
    'Every hour'
  ]
  const options2 = [
    '1 to 31 March',
    '1 to 30 April',
    '1 to 31 May',
    '1 to 30 June'
  ]
  const setHeights = ()=>{
    if(step4)return
    dispatch({type:'SET_HEIGHT',payload:{step:4,height:550}})
    dispatch({type:'SET_FOUR'})
    setShowMore(true)
  }
  useEffect(()=>{
    setShowMore(false)
  },[])
  return (
    <main className="SetupSchedule">
      <h3>{title}</h3>
      <h4>This will determine how often and dhen your employees are paid.</h4>
      <article onClick={setHeights}>
        <label htmlFor="paySchedule">How often do you pay your employees?*</label>
        <ActionButton label={paySchedule} options={options} cb={schedule=>dispatch({type:'PAY_SCHEDULE',payload:schedule})}/>
      </article>
      {showMore&&<div>
        <article>
          <label htmlFor="date">What is there first pay date in QuickBooks?*</label>
          <DatePick selected={payDate} handleDateChange={date=>dispatch({type:'SET_PAY_DATE',payload:date})} />
          <p><em>This is tax month for 2019-20</em></p>
          <p><em>Your employees will be paid on the 28th of every month beginning 28th March</em></p>
        </article>
        <article>
          <label htmlFor="days">What are the days they're being paid for?</label>
          <ActionButton label={payDays} options={options2} cb={days=>dispatch({type:'PAY_DAYS',payload:days})}/>
        </article>
        <article>
          <label htmlFor="name">What are the days they're being paid for?</label>
            <input
              type="text"
              value={scheduleName}
              placeholder="Every Month"
              onChange={e=>dispatch({type:'SCHEDULE_NAME',payload:e.target.value})}/>
        </article>
      </div>}
    </main>
  )
}
export default SetupSchedule
