import React,{useState} from 'react'
import './ReclaimWages.css'

const ReclaimWages = ()=> {
  const [close,setClose] = useState(false)
  if(close)return null
  return (
    <main className="ReclaimWages">
      <button onClick={()=>setClose(true)} className="hi hi-close close"><span className="visuallyHidden">Close</span></button>
      <h4>Need to reclaim wages for furloughed employees?</h4>
      <p>Claim for your furloughed employees wages through the<br/>Coronavirus Job Rentention Scheme.</p>
      <span className="blueLink">Learn more</span>
    </main>
  )
}
export default ReclaimWages
