import React,{ useRef,useEffect,useState } from 'react'
import SelfAssessmentData from '../../../Data/SelfAssessment/SelfAssessment.json'
import Thousands from '../../../Utils/Thousands'
import './BusinessExpenses.css'

const BusinessExpenses = ()=>{
  const [ collapsed,setCollapsed ] = useState(false)
  const box = 17
  const biz = SelfAssessmentData["business expenses"]
  const total = Object.keys(biz).reduce((sum,itm)=>biz[itm]["total"]+sum,0)
  const allow = Object.keys(biz).reduce((sum,itm)=>biz[itm]["disallowable"]+sum,0)
  const [ height,setHeight ] = useState(null)
  const collapsedtHeight = 40
  const vRef = useRef(null)
  useEffect(()=>{
    setHeight(vRef.current.clientHeight - 30)
  },[])
  return (
    <main
      style={{"height":collapsed?collapsedtHeight:height}}
      ref={vRef}
      className={`SATable BusinessExpenses ${collapsed ? 'collapsed':''}`}>
      <h2 onClick={()=>setCollapsed(!collapsed)}>
        <i className="hi hi-chevron-down"/>
        <span>Business expenses</span>
      </h2>
      <table cellSpacing="0">
        <thead>
          <tr>
            <th></th>
            <th colSpan="2">Total expenses</th>
            <th colSpan="2">Disallowable expenses</th>
          </tr>
        </thead>
        <tbody>
          {Object.keys(biz).map((key,i)=>(
            <tr key={key}>
              <td>{key}</td>
              <td>Box {box+i}</td>
              <td>£{Thousands(biz[key]["total"])}</td>
              <td>Box {(Object.keys(biz).length + box)+i+1}</td>
              <td>£{Thousands(biz[key]["disallowable"])}</td>
            </tr>
          )
        )}
        </tbody>
        <tfoot>
          <tr>
            <td>Total</td>
            <td>Box {Object.keys(biz).length + box}</td>
            <td>£{total}</td>
            <td>Box {(Object.keys(biz).length*2) + box+1}</td>
            <td>£{allow}</td>
          </tr>
          <tr>
            <td>Allowable business expenses (Box 31 minus Box 46)</td>
            <td></td>
            <td></td>
            <td></td>
            <td>£{allow+total}</td>
          </tr>
        </tfoot>
      </table>
    </main>
  )
}
export default BusinessExpenses
