import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import OperationsDetailHeader from '../OperationsDetailHeader'
import OperationsDetailFields from '../OperationsDetailFields'
import BonusSearch from '../BonusSearch'
import BonusList from '../BonusList'
import AddBonus from '../AddBonus'
import EditBonusName from '../EditBonusName'
import EditBonus from '../EditBonus'
import './OperationsDetail.css'

const OperationsDetail = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { editBonus } = state
  const handleButton = ()=>{
    dispatch({type:'ANIMATE_Drawer'})
    setTimeout(()=>dispatch({type:'CLOSE_Drawer'}),800)
  }
  return(
    <main className={`OperationsDetail step` + editBonus}>
      <section>
        <aside>
          <OperationsDetailHeader/>
          <OperationsDetailFields/>
          <EditBonus/>
        </aside>
        <aside>
         <AddBonus/>
         <BonusSearch/>
         <BonusList/>
        </aside>
        <aside>
          <EditBonusName/>
        </aside>
      </section>
      <button onClick={handleButton} className="done">Done</button>
    </main>
  )
}
export default OperationsDetail
