import React,{ useState,useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import { Link } from 'react-router-dom'
import NavData from './NavData'
import './Sidenav.css'


const Sidenav = () => {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { base } = state
  const getStartTab = () =>{
    const url = window.location.href.split('/')
    const tab = url[url.length-1]
    let currentTab
    if (tab === '') return 6
    NavData.forEach((t,i)=>{
      if (t.name.toLowerCase() === tab) currentTab = i
    })
    return currentTab
  }

  const [currentNavTab, setCurrentTab] = useState(getStartTab())
  const handleNavClick = e =>{
    dispatch({type:'PAYROLL_PAGE',payload:window.location.href.indexOf('payroll')!== -1 || window.location.href.indexOf('employees')!== -1})
    const idx = Number(e.target.getAttribute('idx'))
    if(idx === currentNavTab) return
    setCurrentTab(idx)
  }

  return (
    <aside className="Sidenav">
      <div className="btnCtn">
        <button className="btn ghost white"><span>New</span></button>
      </div>
      <ul className="SidenavList" onClick={handleNavClick}>
        {NavData.map((itm,i)=>(
          <li className={(currentNavTab === i ? 'current' : null)} key={i}>
            <Link idx={i} to={base+'/'+itm.name.toLowerCase().split(' ').join('')}>{itm.name}</Link>
            {itm.sub&&<i className="hi hi-chevron-right"></i>}
            {itm.sub ? <ul className="sub">
              {itm.sub.map((s,i2)=>(
                <li key={i2}><Link idx={i} to={base+'/'+s.name.toLowerCase().split(' ').join('')}>{s.name}</Link></li>
              ))}
            </ul> : null}
          </li>
        ))}
      </ul>
    </aside>
  )
}

export default Sidenav
