import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './EditBonusName.css'

const EditBonusName = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees,bonusToEdit,editBonus,addPay,bonusTypes,deductionTypes } = state
  if (editBonus < 2) return null
  const { displayName } = employees[currentOperation].operations[bonusToEdit]

  return(
    <main className="EditBonusName">
      <section>
        <header>
          <h4>{displayName || bonusToEdit}</h4>
          <button className="close" onClick={()=>dispatch({type:'BACK_TO_BONUSLIST'})}>
            <i className="hi hi-close"/>
            <span className="visuallyHidden">Back</span>
          </button>
        </header>
        <article className={`BonusItem ${bonusToEdit.split(' ').join('').toLowerCase()}`}>
          <div>
            <h4>{displayName || bonusToEdit}</h4>
            <p>{addPay?bonusTypes[bonusToEdit]:deductionTypes[bonusToEdit]}</p>
          </div>
        </article>
        <div className="field">
          <label htmlFor="field">Name*</label>
          <input
            id="field"
            value={displayName || bonusToEdit}
            type="text"
            onChange={e=>dispatch({type:'UPDATE_LABEL',payload:{
              id:currentOperation,
              operation:bonusToEdit,
              displayName:e.target.value}})}/>
        </div>
      </section>
    </main>
  )
}
export default EditBonusName
