import React,{useContext} from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import Switch from '../../../ui/Switch/'
import EmployeeName from '../EmployeeName'
import Thousands from '../../../Utils/Thousands'
import './OperationsDetailHeader.css'

const OperationsDetailHeader = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees } = state
  const emp = employees[currentOperation]
  const { shouldGetPaid } = emp
  const handleButton = ()=>{
    dispatch({type:'ANIMATE_Drawer'})
    setTimeout(()=>dispatch({type:'CLOSE_Drawer'}),800)
  }
  const getAllDeductions = Object.keys(employees[currentOperation].operations).reduce((sum,itm)=>{
    if (!employees[currentOperation].operations[itm].addition) return sum + employees[currentOperation].operations[itm].amount
    return sum
  },0)
  const getAllPayments = Object.keys(employees[currentOperation].operations).reduce((sum,itm)=>{
    if (employees[currentOperation].operations[itm].addition) return sum + employees[currentOperation].operations[itm].amount
    return sum
  },0)

  return(
    <header className="OperationsDetailHeader">
      <article>
        <Switch
          handleSwitchChange={()=>dispatch({type:'TOGGLE_SHOULD_GET_PAID',payload:currentOperation})}
          checked={shouldGetPaid}
          id={currentOperation}/>
      </article>
      <article>
        <EmployeeName id={currentOperation}/>
      </article>
      <article>
        <div>
          <button className="close" onClick={handleButton}>
            <i className="hi hi-close"/>
            <span className="visuallyHidden">close</span>
          </button>
        </div>
        <div>
          <label>Gross Pay</label>
          <span>£{Thousands(getAllPayments-getAllDeductions.toFixed(2))}</span>
        </div>
      </article>
    </header>
  )
}
export default OperationsDetailHeader
