import React,{ useContext,useEffect } from 'react'
import PayrollDispatchContext from '../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../context/Payroll/PayrollStateContext'
import './Pile.css'

const Pile = ({message})=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  useEffect(()=>{
    setTimeout(()=>{
      dispatch({type:'RMV_MESSAGE'})
    },3000)
    setTimeout(()=>{
      dispatch({type:'ANIMATE_PILE'})
    },2000)
  },[dispatch])
  return (
    <main className={`Pile ${state.amnimatePile ? 'show':''}`}>
      <article>
        <i className="hi hi-circle-check"/>
        <span>{message}</span>
      </article>
    </main>
  )
}
export default Pile
