import PayrollHistory from '../Data/Payroll/payrollHistory.json'
import Employees from '../Data/Payroll/payrun/bonuses.json'
import months from '../Utils/makeDates'

const sortDates = () =>{
  let data = [...PayrollHistory].splice(0,53)
  let processHistory = data.sort((a,b)=> new Date(b.payDate) - new Date(a.payDate))
  processHistory.forEach((itm,idx) => {
    let date = itm.payDate.split('/')
    let m = months[Number(date[0])-1]
    //let day = itm.paySchedule ? (idx%2 ? '15' : '28') : '28'
    itm.payPeriod = getPeriod(m)
  })
  return processHistory
}


const monthlyFilter = data =>{
  let monthly = []
  data.forEach(itm => {
    let year = Number(itm.payDate.split('/')[2].substring(1,4))
    let year0 = year-1
    if (monthly.indexOf(String(year0 + '-' + year)) === -1) {
      monthly.push(year0 + '-' + year)
    }
  })
  return monthly
}

const getPeriod = month => {
  let period
  switch (month) {
    case 'jan':
    case 'mar':
    case 'may':
    case 'jul':
    case 'aug':
    case 'oct':
    case 'dec': period = '1 - 31';break;
    case 'feb': period = '1 - 29';break;
    default: period = '1 - 30'
  }
  return period
}

let initialPayrollState = {
  history: sortDates(),
  taxYears: monthlyFilter(sortDates()),
  paySchedules: sortDates(),
  period: 'All time',
  schedule: 'All',
  max:10,
  breakdown: false,
  showHistory: false,
  amnimatePile: false,
  payrollDeleted:false,
  DrawerCanBeOpen: true,
  loading: false,
  showSchedule: false,
  employees:Employees,
  payDate: new Date(),
  addPay: true,
  searchValue:'',
  percent:14.5,
  editBonus:0,
  reviewNetPay:0,
  payrollPage:null,
  bonusToEdit:null,
  Drawer:null,
  bonusTypes: {
    "Bonus":"An extra payment for a job well done",
    "Car allowance":"Any other taxable pay, such as commission on a car allowance",
    "Overtime":"Pay for time worked beyond normal hours",
    "Commission":"Any other taxable pay, such as commission on a car allowance",
    "Expense reimbursement":"Repayment for mileage or other business expenses (non-taxable)",
    "Mileage reimbursement":"Repayment for mileage or other business expenses (non-taxable)"
  },
  deductionTypes: {
    "Advance repayment":"Repayment of a long-term loan or a one-time advance",
    "Loan repayment":"Repayment of a long-term loan or a one-time advance"
  },
  base:'/self-assessment/',
  DrawerOpen:false,
  editBonusDrawer:false,
  animateCloseDrawer:false,
  currentOperation: null
  //DrawerOpen:true,
  //animateCloseDrawer:true,
  //currentOperation:'dfg65h465d4fg'
}

const filterByYear = (state,action)=> {
  let selected = action.payload.substring(3,5)
  let currentTaxYear = initialPayrollState.history.filter(itm => itm.payDate.split('/')[2].substring(2,4) === selected)
  return currentTaxYear
}

const hideDropdown = state =>{
  if (state.showHistory) state.showHistory = false
  if (state.showSchedule) state.showSchedule = false
}

class Bonus {
  constructor(addition,amount,isActive){
    this.addition = addition
    this.amount = amount
    this.isActive = isActive
  }
}

const payrollReducer = (state, action) => {
  switch (action.type) {

    case 'PAYROLL_PAGE':
      let PAYROLL_PAGE = {...state}
      PAYROLL_PAGE.payrollPage = action.payload
    return PAYROLL_PAGE;

    case 'BACK_TO_BONUSLIST':
      let BACK_TO_BONUSLIST = {...state}
      BACK_TO_BONUSLIST.editBonus = 1
    return BACK_TO_BONUSLIST;

    case 'TOGGLE_BREAKDOWN':
      let TOGGLE_BREAKDOWN = {...state}
      TOGGLE_BREAKDOWN.breakdown = !TOGGLE_BREAKDOWN.breakdown
    return TOGGLE_BREAKDOWN;

    case 'ANIMATE_PILE':
      let ANIMATE_PILE = {...state}
      ANIMATE_PILE.amnimatePile = !ANIMATE_PILE.amnimatePile
    return ANIMATE_PILE;

    case 'RMV_MESSAGE':
      let RMV_MESSAGE = {...state}
      RMV_MESSAGE.payrollDeleted = false
    return RMV_MESSAGE;

    case 'DELETE_PAYROLL':
      let DELETE_PAYROLL = {...state}
      DELETE_PAYROLL.payrollDeleted = true
    return DELETE_PAYROLL;

    case 'DOWNLOAD_CSV':
      let DOWNLOAD_CSV = {...state}
      console.log('DOWNLOAD_CSV');
    return DOWNLOAD_CSV;

    case 'REVIEW_NET_PAY':
      let REVIEW_NET_PAY = {...state}
      REVIEW_NET_PAY.reviewNetPay = action.payload
    return REVIEW_NET_PAY;

    case 'EDIT_BONUS_NAME':
    console.log('EDIT_BONUS_NAME');
      let EDIT_BONUS_NAME = {...state}
      EDIT_BONUS_NAME.editBonusName = true
      EDIT_BONUS_NAME.bonusToEdit = action.payload
      EDIT_BONUS_NAME.editBonus = 2
    return EDIT_BONUS_NAME;

    case 'USE_BONUS':
      let USE_BONUS = {...state}
      const { currentOperation,bonus } = action.payload
      //USE_BONUS.employees[currentOperation][bonus].isActive
      if (USE_BONUS.employees[currentOperation].operations[bonus]) {
        USE_BONUS.employees[currentOperation].operations[bonus].isActive = !USE_BONUS.employees[currentOperation].operations[bonus].isActive
      } else {
        USE_BONUS.employees[currentOperation].operations[bonus] = new Bonus(USE_BONUS.addPay,0.00,true)
      }
    return USE_BONUS;

    case 'BACK_TO_BONUS_FIELDS':
    console.log('BACK_TO_BONUS_FIELDS');
      let BACK_TO_BONUS_FIELDS = {...state}
      BACK_TO_BONUS_FIELDS.editBonus = 0
    return BACK_TO_BONUS_FIELDS;

    case 'ADD_BONUS':
    console.log('ADD_BONUS');
      let ADD_BONUS = {...state}
      ADD_BONUS.editBonus = 1
      ADD_BONUS.addPay = action.payload
    return ADD_BONUS;

    case 'SUBMIT_BONUS_SEARCH':
      let SUBMIT_BONUS_SEARCH = {...state}
      SUBMIT_BONUS_SEARCH.searchValue = action.payload
    return SUBMIT_BONUS_SEARCH;

    case 'ANIMATE_Drawer':
      let ANIMATE_Drawer = {...state}
      if (!ANIMATE_Drawer.animateCloseDrawer && ANIMATE_Drawer.DrawerCanBeOpen) {
        ANIMATE_Drawer.animateCloseDrawer = true
        ANIMATE_Drawer.DrawerCanBeOpen = false
      } else {
        ANIMATE_Drawer.animateCloseDrawer = false
      }
    return ANIMATE_Drawer;

    case 'CLOSE_Drawer':
    console.log('CLOSE_Drawer');
      let CLOSE_Drawer = {...state}
      CLOSE_Drawer.DrawerCanBeOpen = true
      CLOSE_Drawer.DrawerOpen = false
      CLOSE_Drawer.editBonus = 0
      CLOSE_Drawer.currentOperation = null
      CLOSE_Drawer.Drawer = null
    return CLOSE_Drawer;

    case 'EDIT_OPERATIONS':
      let EDIT_OPERATIONS = {...state}
      EDIT_OPERATIONS.currentOperation = action.payload.id
      EDIT_OPERATIONS.Drawer = action.payload.Drawer
      EDIT_OPERATIONS.DrawerOpen = true
    return EDIT_OPERATIONS;

    case 'UPDATE_LABEL':
      let UPDATE_LABEL = {...state}
      UPDATE_LABEL.employees[action.payload.id].operations[action.payload.operation].displayName = action.payload.displayName
    return UPDATE_LABEL;

    case 'UPDATE_OPERATIONS':
      let UPDATE_OPERATIONS = {...state}
      UPDATE_OPERATIONS.employees[action.payload.id].operations[action.payload.operation].amount = Number(action.payload.amount)
    return UPDATE_OPERATIONS;

    case 'SET_PAY_DATE':
      let SET_PAY_DATE = {...state}
      SET_PAY_DATE.payDate = action.payload
    return SET_PAY_DATE;

    case 'TOGGLE_SHOULD_GET_PAID':
      let TOGGLE_SHOULD_GET_PAID = {...state}
      TOGGLE_SHOULD_GET_PAID.employees[action.payload].shouldGetPaid = !TOGGLE_SHOULD_GET_PAID.employees[action.payload].shouldGetPaid
    return TOGGLE_SHOULD_GET_PAID;

    case 'resetPeriod':
      let resetTaxyear = {...state}
      resetTaxyear.history = initialPayrollState.history
      resetTaxyear.period = 'All time'
      hideDropdown(resetTaxyear)
      return resetTaxyear;

    case 'filterByTaxYear':
      let taxYearState = {...state}
      taxYearState.history = filterByYear(state,action)
      taxYearState.period = action.payload + ' tax year'
      hideDropdown(taxYearState)
      return taxYearState;

    case 'monthly':
    let mState = {...state}
      mState.schedule = 'Monthly'
      hideDropdown(mState)
      return mState;

    case 'weekly':
    let wState = {...state}
      wState.schedule = 'Weekly'
      hideDropdown(wState)
      return wState;

    case 'showAll':
    let allState = {...state}
    allState.schedule = 'All'
    hideDropdown(allState)
    return allState;

    case 'showMore':
    let showMore = {...state}
    showMore.max = state.max + 10
    hideDropdown(showMore)
    return showMore;

    case 'showHistory':
    let showHistory = {...state}
    if (state.showSchedule) showHistory.showSchedule = false
    showHistory.showHistory = !state.showHistory
    return showHistory;

    case 'showSchedule':
    let showSchedule = {...state}
    if (state.showHistory) showSchedule.showHistory = false
    showSchedule.showSchedule = !state.showSchedule
    return showSchedule;

    default: throw new Error('Unexpected action');
  }
}
export default payrollReducer
export { initialPayrollState }
