import React,{ useContext, forwardRef, useEffect } from 'react'
import './Slider.css'
import TileShadow from '../../ui/TileShadow'
import PayrollDispatchContext from '../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../context/Payroll/PayrollStateContext'

const FwSlider = ({ children },ref) => {

  const dispatch = useContext(PayrollDispatchContext)
  const { heights,length,width,current } = useContext(PayrollStateContext)

  const getStepsDimensions = () => {

    const steps = [...ref.current.children[0].children]

    let heights = []
    steps.forEach(itm => heights.push(itm.children[0].clientHeight))

    dispatch({
      type:'setDimensions',
      payload: {
        length:children.length,
        heights: heights,
        width: ref.current.clientWidth
      }
    })

  }

  useEffect(getStepsDimensions,[length])
  return (
    <TileShadow>
      <main
        ref={ref}
        style={{'height':heights[current]}}
        className="Slider">
          <section className="SliderInner" style={{
            'width': width * length,
            'transform': 'translateX(-'+ width * current +'px)'
          }}>{children}</section>
      </main>
    </TileShadow>
  )
}

const Step = ({ children }) => {
  return <article>{children}</article>
}

const Slider = forwardRef(FwSlider)
export default Slider
export { Step }
