import React, { useReducer } from 'react'
import Tile from '../../../../ui/Tile/Tile'
import ReadyForPayDay from './../ReadyForPayDay'
import NxtPayroll from './../NxtPayroll'
import HMRCReportsEmptyState from './../HMRCReportsEmptyState'
import PayrollCostEmptyState from './../PayrollCostEmptyState'
import Pension from './../Pension'
import TipsEmptyState from './../TipsEmptyState'
import History from './../History'
import ImportData from './../../ImportData'
import Trowser from '../../../../ui/Popups/Trowser'
import '../../Payroll.css'
import PayrollStateContext from '../../../../context/Payroll/PayrollStateContext'
import PayrollDispatchContext from '../../../../context/Payroll/PayrollDispatchContext'
import readyToPayroll, { initialPayrollState } from '../../../../reducers/readyToPayroll'

const ReadyToPayRoll =()=>{
  const [state, dispatch] = useReducer(readyToPayroll, initialPayrollState)
  return (
    <PayrollDispatchContext.Provider value={dispatch}>
      <PayrollStateContext.Provider value={state}>
        <main className="ReadyToPayRoll">
          <Trowser open={state.trowserOpen}><ImportData/></Trowser>
          <div className="linkCtn">
            <span className="blueLink">Need help with Payroll?</span>
          </div>
            <Tile><ReadyForPayDay/></Tile>
            <section className="payrollHomeGrid">
              <Tile title="Next payroll"><NxtPayroll/></Tile>
              <Tile title="HMRC reports"><HMRCReportsEmptyState/></Tile>
              <Tile cssClass="totalCost" title="Total payroll cost"><PayrollCostEmptyState/></Tile>
              <Tile title="Workplace pension"><Pension/></Tile>
              <Tile cssClass="Tips" title="Tips"><TipsEmptyState/></Tile>
            </section>
            <History/>
          </main>
        </PayrollStateContext.Provider>
      </PayrollDispatchContext.Provider>
    )
}
export default ReadyToPayRoll
