import React,{useContext} from 'react'
import EstimatedIncome from './EstimatedIncome'
import BusinessIncome from './BusinessIncome'
import BusinessExpenses from './BusinessExpenses'
import NetProfit from './NetProfit'
import BackLink from '../../ui/BackLink'
import NationalIsurance from './NationalIsurance'
import SelfAssessmentDispatchContext from '../../context/SelfAssessment/SelfAssessmentDispatchContext'
import './SelfAssessment.css'

const SelfAssessment = ()=> {
  const dispatch = useContext(SelfAssessmentDispatchContext)
  return (
    <main className="SelfAssessment">
      <p onClick={()=>dispatch({type:'CLOSE_SA_TROWSER'})}><BackLink path="/selfassessment" txt="Taxes"/></p>
      <EstimatedIncome/>
      <section className="SelfAssessmentTitle">
        <h1>Self Assessment summary</h1>
        <span className="blueLink">How to submit your self assessment</span>
      </section>
      <BusinessIncome/>
      <BusinessExpenses/>
      <NetProfit/>
      <NationalIsurance/>
    </main>
  )
}
export default SelfAssessment
