import React,{useState} from 'react'
import './ActionButton.css'
const ActionButton =({label,options,cb})=>{
  const [ selected,setSelected ] = useState(null)
  const [ open,setOpen ] = useState(false)
  const handleDropdownClick = e =>{
    setOpen(true)
    if (e.target.nodeName === 'LI') {
      const val = e.target.innerText
      setSelected(val)
      cb(val)
      setOpen(false)
    }
  }
  return(
    <div className="ActionButton" onClick={handleDropdownClick}>
      <span>{label}</span>
      <i className="hi hi-chevron-down"/>
      {open&&<ul onMouseLeave={()=>setOpen(false)} >{options.map((opt,i)=><li className={selected === opt?'selected':''} key={i+'option'}>{opt}</li>)}</ul>}
    </div>
    )
}
export default ActionButton
