import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './NextSteps.css'

const NextSteps = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { currentOperation } = state
  const handleEditClick = trous=> {
    dispatch({type:'EDIT_OPERATIONS',payload:{id:currentOperation,Drawer:trous}})
    setTimeout(()=>dispatch({type:'ANIMATE_Drawer'}),5)
  }
  return (
    <main className="NextSteps">
      <h3>Next Steps</h3>

      <article>
        <div>
          <h4>Get/send payslips</h4>
          <p>Download or email payslips to your employees.</p>
          <small><span className="blueLink">Use secure passwords</span> <span>to email payslips</span></small>
        </div>
        <aside><div><button onClick={()=>handleEditClick('payslips')} className="btn"><span>Get payslips</span></button></div></aside>
      </article>

      <article>
        <div>
          <h4>FPS is due on pay day</h4>
          <p>Full Payment Submission is due on the pay day. Submit now to avoid penalties.</p>
        </div>
        <aside><div><button onClick={()=>handleEditClick('FPS')} className="btn ghost"><span>Submit FPS</span></button></div></aside>
      </article>

      <article>
        <div>
          <h4>Download payroll details (Gross to Net) report</h4>
          <p>Curious about payroll details like employee payment, taxes, NI and other deductions?</p>
        </div>
        <aside><div><button className="btn ghost"><span>Download report</span></button></div></aside>
      </article>
    </main>
  )
}
export default NextSteps
