import React from 'react'
import './FPSHeader.css'

const FPSHeader = ({ handleCloseClick }) =>{

  return (
    <main className="FPSHeader">
      <header>
        <button onClick={handleCloseClick} className="close">
          <i className="hi hi-close"/>
        </button>
        <div className="titleSub">
          <h3>FPS</h3>
          <h4>(Full Payment Submission)</h4>
        </div>

        <table className="status">
          <thead>
            <tr>
              <th>Status</th>
              <th>Tax period</th>
              <th>Due</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td><em>Overdue</em></td>
              <td>Month 3</td>
              <td><em>22 Jun</em></td>
            </tr>
          </tbody>
        </table>

      </header>

    </main>
  )
}
export default FPSHeader
