let initialSelfAssessmentState = {
  viewEstimate:false,
  viewModal:false,
}

const selfAssessmentReducer = (state, action) => {
  switch (action.type) {

    case 'OPEN_SA_MODAL':
      let OPEN_SA_MODAL = {...state}
      OPEN_SA_MODAL.viewModal = true
    return OPEN_SA_MODAL;

    case 'CLOSE_SA_MODAL':
      let CLOSE_SA_MODAL = {...state}
      CLOSE_SA_MODAL.viewModal = false
    return CLOSE_SA_MODAL;

    case 'OPEN_SA_TROWSER':
      let OPEN_SA_TROWSER = {...state}
      OPEN_SA_TROWSER.viewEstimate = true
      OPEN_SA_TROWSER.viewModal = false
    return OPEN_SA_TROWSER;

    case 'CLOSE_SA_TROWSER':
      let CLOSE_SA_TROWSER = {...state}
      CLOSE_SA_TROWSER.viewEstimate = false
    return CLOSE_SA_TROWSER;

    default: throw new Error('Unexpected action');
  }
}

export default selfAssessmentReducer
export { initialSelfAssessmentState }
