import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import EmployeeName from '../EmployeeName'
import Thousands from '../../../Utils/Thousands'
import './ReviewEmployee.css'

const ReviewEmployee = ({id})=>{
    const dispatch = useContext(PayrollDispatchContext)
    const state = useContext(PayrollStateContext)
    const { employees,percent } = state
    const getAllPayments = Object.keys(employees[id].operations).reduce((sum,itm)=>{
      if (employees[id].operations[itm].addition) return sum + employees[id].operations[itm].amount
      return sum
    },0)
    const getAllDeductions = Object.keys(employees[id].operations).reduce((sum,itm)=>{
      if (!employees[id].operations[itm].addition) return sum + employees[id].operations[itm].amount
      return sum
    },0)
    const taxes = (getAllPayments - getAllDeductions)*percent/100

    const handleReviewClick = id => {
      dispatch({type:'EDIT_OPERATIONS',payload:{id,Drawer:'review'}})
      setTimeout(()=>dispatch({type:'ANIMATE_Drawer'}),5)
    }
  return(
    <main className="ReviewEmployee" onClick={()=>handleReviewClick(id)}>
      <EmployeeName id={id}/>
      <div className="controls">
        <section className="enableEdit">
          <div className="calculate">
            <article>
              <label>Gross pay</label>
              <span>£{Thousands((getAllPayments - getAllDeductions).toFixed(2))}</span>
            </article>
            <article>
              <label>Taxes and deductions</label>
              <span>£{Thousands(taxes.toFixed(2))}</span>
            </article>
            <article>
              <label>Net pay</label>
              <strong className="blueLink">£{Thousands(((getAllPayments - getAllDeductions) - taxes).toFixed(2))}</strong>
            </article>
          </div>
        </section>
      </div>
    </main>
  )
}
export default ReviewEmployee
