import React from 'react'
import tips from '../../../../assets/images/tips.svg'
import './Tips.css'
const Tips = ()=> (
  <article>
    <button className="refresh"><i className="hi hi-refresh"></i><span>Refresh</span></button>
    <div className="imgCtn">
      <img src={tips} alt="tips"/>
    </div>
    <p>Has an employee taken paternity time off? Track and process this in your payroll</p>
    <span className="blueLink">Learn more</span>
  </article>
)
export default Tips
