import React,{useContext} from 'react'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import DetailFieldItem from '../DetailFieldItem'
import './OperationsDetailFields.css'

const OperationsDetailFields = ()=>{
  const state = useContext(PayrollStateContext)
  const { currentOperation,employees } = state
  const emp = employees[currentOperation]
  const uniqueKey = key => currentOperation+key.split(' ').join('').toLowerCase()
  const { operations } = emp
  const opx = addition => {
    if (addition) {
      return Object.keys(operations).filter(key=>operations[key].addition)
    } else {
      return Object.keys(operations).filter(key=>!operations[key].addition)
    }
  }
  return(
    <main className="OperationsDetailFields">
      <section>
        <h4>Payments</h4>
        {opx(true).filter(key=>operations[key].isActive).map(key=><DetailFieldItem  operation={key} id={uniqueKey(key)} key={uniqueKey(key)}/>)}
        {opx(false).filter(key=>operations[key].isActive).length > 0 &&(
          <>
            <h4>Deductions</h4>
            {opx(false).filter(key=>operations[key].isActive).map(key=><DetailFieldItem  operation={key} id={uniqueKey(key)} key={uniqueKey(key)}/>)}
          </>
        )}
      </section>
    </main>
  )
}
export default OperationsDetailFields
