import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import EmployeePayslip from '../EmployeePayslip'
import './Payslips.css'

const Payslips = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const handleCloseClick = ()=>{
    dispatch({type:'ANIMATE_Drawer'})
    setTimeout(()=>dispatch({type:'CLOSE_Drawer'}),800)
  }

  return (
    <main className="Payslips">
      <header>
        <h3>Employee payslip</h3>
        <button onClick={handleCloseClick} className="close">
          <i className="hi hi-close"/>
        </button>
      </header>
      <section>
        <h2>Send payslips to employees</h2>
        <p>
          <span>Emailing payslips?</span>
          <span className="blueLink">Give employees secure passwords</span>
        </p>
        <section className="EmployeeList">
          {Object.keys(state.employees).map(key=><EmployeePayslip key={key} id={key}/>)}
        </section>
        <div className="btnCtn">
          <button className="btn ghost"><span>Send payslips</span></button>
          <button className="btn ghost"><span>Download all payslips</span></button>
        </div>
      </section>

    </main>
  )
}
export default Payslips
