import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import RadioGroup from '../../../ui/RadioGroup'
import './EmployersBill.css'
const EmployersBill = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { billType } = state
  const options = [
    {
      name:"I qualify",
      label:"Yes, I qualify"
    },
    {
      name:"I dont qualify",
      label:"No, I don't qualify"
    }
  ]
  const handleRadio = e => {
    dispatch({type:'SET_HEIGHT',payload:{step:6,height:340}})
    dispatch({type:'SELECT_BILL_TYPE',payload:e.target.name})
  }
  const yes = `We'll let HMRC know and update the amount you need to pay over th HMRC, by reflecting the allowance you can claim each tax month. You can find this on the "Employment Payment Record(P32)" in the Reports tab in QuickBooks.`
  const no = "No worries, this just means you'll paying the normal employers' National Insurance contributions. You can always change this later if you become qualified."
  return (
    <main className="EmployersBill">
      <h3>{`Lastly, let's figure out if you can save in your employers' National Insurance Bill`}</h3>
      <label>Do you qualify for Employment Allowance?*</label>
      <p>You can figure out if you qualify by checking <span className="blueLink">here</span>.</p>
      <RadioGroup
        group={options}
        label={null}
        selected={billType}
        handleRadioClick={e=>handleRadio(e)}/>
      {billType&&(
        <p>
          {billType === 'I qualify' ? yes : no}
        </p>
      )}
    </main>
  )
}
export default EmployersBill
