import React from 'react'
import './Radio.css'

const Radio = ({checked,id,children,handleRadioClick,name})=><div className="Radio">
  <input className="visuallyHidden" onChange={handleRadioClick} type="radio" id={id} name={name} checked={checked}/>
  <span className={checked ? 'uiRadio checked' : 'uiRadio'}></span>
  <label htmlFor={id}>{children}</label>
</div>
export default Radio
