import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './DeleteFile.css'
const DeleteFile = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { payrollFile } = state
  return (
    <div className="DeleteFile">
      <span>
        <i className="hi hi-circle-check"/>
        <span>{payrollFile}</span>
      </span>
      <span className="delete" onClick={()=>dispatch({type:'DELETE_DROP_FILE'})}>
        <i className="hi hi-delete"/>
        <span className="blueLink">Delete file</span>
      </span>
    </div>
  )
}
export default DeleteFile
