import React,{ useEffect,useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import RadioGroup from '../../../ui/RadioGroup'
import './SortPension.css'
const SortPension = ()=> {
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { pensionType,pensionPlan } = state
  const options = [
    {
      name:"director only",
      label:"I'm a director-only business"
    },
    {
      name:"under SPA",
      label:"My employees are all under 21 or over the SPA (State Pension Age) or they earen less than £10k/year or the equivalent in pay period"
    },
    {
      name:"over SPA",
      label:"Some or all of my employees are between 21-SPA years old and they earn more than 10k? year or the equivalent ina pay period"
    }
  ]
  const plan = [
    {
      name:"plan",
      label:"Yes"
    },
    {
      name:"noplan",
      label:"No, not yet"
    }
  ]
  useEffect(()=>{
    let height
    if (pensionType === 'over SPA') {
      height=550
      if (pensionPlan === 'plan') height=600
      if (pensionPlan === 'noplan') height=620
    } else {
      height=400
    }
    dispatch({type:'SET_HEIGHT',payload:{step:5,height}})
  },[pensionType,pensionPlan])
  return (
    <main className="SortPension">
      <h3>Let's sort out your employees's workplace pension</h3>
      <p>Most businesses qualify as needing to have a workplace pension for their employees. This can also be known as auto enrolment. Learn more about <span className="blueLink">workplace pensions</span> or setting up <span className="blueLink">multiple workplace pensions</span>.</p>
      <RadioGroup
        group={options}
        label="What best describes your employees?*"
        selected={pensionType}
        handleRadioClick={e=>dispatch({type:'SELECT_PENSION_TYPE',payload:e.target.name})}/>
      {pensionType==='over SPA'&&(
        <>
          <div className="info">
            <i className="hi hi-info"/>
            <p>Based on your info, it looks like the Pensions Regulator requires your business to have a workplace pension scheme.</p>
          </div>
          <RadioGroup
            group={plan}
            label="Do you already have a workplace pension plan for your employees?*"
            selected={pensionPlan}
            handleRadioClick={e=>dispatch({type:'SELECT_PENSION_PLAN',payload:e.target.name})}/>
          {pensionPlan === 'plan'&&<span className="blueLink">Manage workplace pensions</span>}
          {pensionPlan === 'noplan'&&<p>Don't worry the Pensions Regulator gives you up to 3 months from your first employee's start date date to setup with a workplace pension scheme. You can still run payroll without this up until this time. Once you have a plan, we'll help you get et up. <span className="blueLink">More info</span></p>}
        </>
      )}
    </main>
  )
}
export default SortPension
