import React,{useContext} from 'react'
import moment from 'moment'
import { Link } from "react-router-dom"
import BackLink from '../../../ui/BackLink'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './DraftHeader.css'

const DraftHeader = ()=>{
  const state = useContext(PayrollStateContext)
  const { period,schedule,payDate,base } = state
  return(
    <main className="DraftHeader">
      <section>
        <BackLink path="/employees/" txt="Employee pay"/>
        <h1>Does everything look right?</h1>
        <table className="headTable" cellSpacing="0">
          <thead>
            <tr>
              <th>Pay schedule</th>
              <th>Pay period</th>
              <th>Pay date</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td><span>{schedule}</span></td>
              <td><span>{period}</span></td>
              <td><span>{moment(payDate).format('DD MMM YYYY')}</span></td>
            </tr>
          </tbody>
        </table>
      </section>
      <aside>
        <article>
          <p>Draft saved: <span>{moment(new Date()).format('DD MMM YYYY')}</span></p>
          <span className="blueLink">Delete draft</span>
        </article>
        <div><button className="btn"><Link to={base+'/employees/confirm'}><span>Confirm and submit</span></Link></button></div>
      </aside>
    </main>
  )
}
export default DraftHeader
