import React,{useState} from 'react'
import './QBSelect.css'
const QBSelect =({label,options,cb,top})=>{
  const [ selected,setSelected ] = useState(null)
  const [ open,setOpen ] = useState(false)
  const handleDropdownClick = e =>{
    setOpen(true)
    if (e.target.nodeName === 'LI') {
      const val = e.target.innerText
      setSelected(val)
      if(cb)cb(val)
      setOpen(false)
    }
  }
  return(
    <div className={`QBSelect ${top ? 'top':''}`} onClick={handleDropdownClick}>
      <span className="label">{selected || label}</span>
      <i className="hi hi-chevron-down"/>
      {open&&<ul onMouseLeave={()=>setOpen(false)} >{options.map((opt,i)=><li className={selected === opt?'selected':''} key={i+'option'}>{opt}</li>)}</ul>}
    </div>
    )
}
export default QBSelect
