import React,{useState} from 'react'
import './Helpful.css'

const Helpful = ()=> {
  const [selected,setSelected] = useState(null)
  const handleSelect = e =>{
    if (e.target.classList.contains('hi')) {
      if (e.target.classList.contains('hi-thumb-up')) {
        setSelected(0)
      } else {
        setSelected(1)
      }
    }
  }
  return (
    <span onClick={handleSelect} className="Helpful">
      <i className={`hi hi-thumb-up ${selected === 0 ? 'selected':''}`}/>
    <i className={`hi hi-thumb-down ${selected === 1 ? 'selected':''}`}/>
    </span>
  )
}
export default Helpful
