import React,{ useContext } from 'react'
import { Link } from 'react-router-dom'
import PayrollStateContext from '../../../../context/Payroll/PayrollStateContext'
import './NxtPay.css'
const NxtPay = ()=> {
  const state = useContext(PayrollStateContext)
  const { period,schedule,base } = state
  return (
    <article className="NxtPay">
      <div className="topCorner">
        <Link className="blueLink" to={base+'/employees'}>Manage</Link>
      </div>
      <strong className="overdue">Overdue</strong>
      <div className="btnGroup">
        <button className="btn"><Link to={base+'/employees'}><span>Let's go</span></Link></button>
        <button className="btn arrow"><span>▾</span></button>
      </div>
      <span className="frequency">{schedule}</span>
      <small className="period">{period}</small>
    </article>
  )
}
export default NxtPay
