import React,{ useContext } from 'react'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import Initials from '../Initials'
import Thousands from '../../../Utils/Thousands'
import './EmployeePayslip.css'

const EmployeePayslip = ({id})=>{
    const dispatch = useContext(PayrollDispatchContext)
    const state = useContext(PayrollStateContext)
    const { employees } = state
    const { name } = employees[id]
    const getAllDeductions = Object.keys(employees[id].operations).reduce((sum,itm)=>{
      if (!employees[id].operations[itm].addition) return sum + employees[id].operations[itm].amount
      return sum
    },0)
    const getAllPayments = Object.keys(employees[id].operations).reduce((sum,itm)=>{
      if (employees[id].operations[itm].addition) return sum + employees[id].operations[itm].amount
      return sum
    },0)
    const { shouldGetPaid } = employees[id]

  return(
    <section className="EmployeePayslip">
      <div className="controls">
        <input onChange={()=>dispatch({type:'TOGGLE_SHOULD_GET_PAID',payload:id})} type="checkbox" checked={shouldGetPaid}/>
        <section className={`enableEdit ${!shouldGetPaid ? 'disabled':''}`}>
          <div>
            <section className="EmployeeName">
              <Initials name={name}/>
              <div>
                <h4>{name}</h4>
                <p>{name.split(' ')[0].substring(0,1).toLowerCase() + name.split(' ')[1].toLowerCase() }@business.com</p>
              </div>
            </section>
          </div>
        </section>
      </div>
      <div className="netPay">
        <label>Net pay</label>
        <strong className="blueLink">£{Thousands(getAllPayments-getAllDeductions.toFixed(2))}</strong>
      </div>
    </section>
  )
}
export default EmployeePayslip
