import React,{useState} from 'react'
import './Note.css'

const Note = ({theme,children})=>{
  const [show,setShow] = useState(true)
  if(!show)return null
  return (
    <section className={`Note ${theme}`}>
      <main>
        {theme=== 'blue'&& <i className="blueI">i</i>}
        {children}
      </main>
      <aside>
        <button onClick={()=>setShow(false)} className="hi hi-close"/>
      </aside>
    </section>
  )
}

export default Note
