import React from 'react'
import il_life_celebrate_02 from '../../../assets/images/il_life_celebrate_02.svg'
import './NicelyPlayed.css'
const NicelyPlayed = ()=> {
  const infos = [
    'Company Tax Details',
    'Employee(s) Personal Details',
    'Company Student Loan Details',
    'Employee(s) Tax Details',
    'Employee(s) Pay History'
  ]
  return(
    <main className="NicelyPlayed">
      <h3>Nicely played - your data was imported</h3>
      <section>
        <main>
          <h4>We added the following payroll info from your file:</h4>
          <ul>
            {infos.map(itm=><li key={itm}>
              <i className="hi hi-checkmark"/>
              <span>{itm}</span>
            </li>)}
          </ul>
        </main>
        <aside>
          <img src={il_life_celebrate_02} width="200" height="200" alt="il_life_celebrate_02"/>
        </aside>
      </section>
    </main>
  )
}
export default NicelyPlayed
