import React,{useContext} from 'react'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import BonusItem from '../BonusItem'
import NothingHere from '../NothingHere'
import './BonusList.css'

const BonusList = ()=>{
  const state = useContext(PayrollStateContext)
  const { addPay,searchValue,bonusTypes,deductionTypes } = state
  let bonusList  = addPay ? bonusTypes : deductionTypes
  return(
    <main className="BonusList">
      {Object.keys(bonusList)
        .filter(b=>b.toLowerCase().indexOf(searchValue.toLowerCase()) !== -1)
        .map(b=><BonusItem key={b.split(' ').join('').toLowerCase()} bonus={b}/>)}
        {Object.keys(bonusList)
          .filter(b=>b.toLowerCase().indexOf(searchValue.toLowerCase()) !== -1).length === 0 &&(
            <NothingHere/>
          )}
    </main>
  )
}
export default BonusList
