import React,{ useContext,useState,useEffect } from 'react'
import moment from 'moment'
import Thousands from '../../../Utils/Thousands'
import PayrollDispatchContext from '../../../context/Payroll/PayrollDispatchContext'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'
import './PeriodSum.css'

const PeriodSum = ()=>{
  const dispatch = useContext(PayrollDispatchContext)
  const state = useContext(PayrollStateContext)
  const { period,schedule,payDate,breakdown,employees } = state
  const [ pay,setPay ] = useState(0)
  const getAllEmployeePayments = emp => Object.keys(employees[emp].operations).reduce((sum,itm)=>{
    if (employees[emp].operations[itm].addition&&employees[emp].shouldGetPaid) return sum + employees[emp].operations[itm].amount
    return sum
  },0)
  const getAllEmployeeDeductions = emp => Object.keys(employees[emp].operations).reduce((sum,itm)=>{
    if (!employees[emp].operations[itm].addition&&employees[emp].shouldGetPaid) return sum + employees[emp].operations[itm].amount
    return sum
  },0)
  const setIt = ()=>setPay(Object.keys(employees).reduce((sum,emp)=> sum + getAllEmployeePayments(emp)-getAllEmployeeDeductions(emp),0))
  useEffect(()=>setIt())
  return(
    <table className="PeriodSum headTable" cellSpacing="0">
      <thead>
        <tr>
          <th>Pay schedule</th>
          <th>Pay period</th>
          <th>Pay date</th>
          <th>Tax period</th>
          <th>Payroll total</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td><span>{schedule}</span></td>
          <td><span>{period}</span></td>
          <td><span>{moment(payDate).format('DD MMM YYYY')}</span></td>
          <td><span>M3</span></td>
          <td>
            <div><span className="bigNumber">£{Thousands(pay.toFixed(2))}</span></div>
            <button onClick={()=>dispatch({type:'TOGGLE_BREAKDOWN'})} className="viewBreakDown">
              <i className={`hi blueLink ${breakdown ?'hi-chevron-up':'hi-chevron-down'}`}/>
              <span className="blueLink">View breakdown</span>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  )
}
export default PeriodSum
