import React,{ useRef,useEffect,useState } from 'react'
import SelfAssessmentData from '../../../Data/SelfAssessment/SelfAssessment.json'
const NetProfit = ()=>{
  const [ collapsed,setCollapsed ] = useState(false)
  const biz = SelfAssessmentData["business income"]
  const exp = SelfAssessmentData["business expenses"]
  const totalBizDis = Object.keys(biz).reduce((sum,itm)=>biz[itm]["disallowable"]+sum,0)
  const totalBizTot = Object.keys(exp).reduce((sum,itm)=>exp[itm]["total"]+sum,0)
  const totalExp = Object.keys(exp).reduce((sum,itm)=>exp[itm]["disallowable"]+sum,0)
  const [ height,setHeight ] = useState(null)
  const collapsedtHeight = 40
  const vRef = useRef(null)
  useEffect(()=>{
    setHeight(vRef.current.clientHeight - 30)
  },[])
  return (
    <main
      style={{"height":collapsed?collapsedtHeight:height}}
      ref={vRef}
      className={`SATable NetProfit ${collapsed ? 'collapsed':''}`}>
      <h2 onClick={()=>setCollapsed(!collapsed)}>
        <i className="hi hi-chevron-down"/>
        <span>Net profit or loss</span>
      </h2>
      <table cellSpacing="0">
        <tbody>
          <tr>
            <td>Business income</td>
            <td></td>
            <td></td>
            <td></td>
            <td>£{totalBizDis}</td>
          </tr>
          <tr>
            <td>Allowable business expenses (Box 31 minus 46) </td>
            <td></td>
            <td></td>
            <td></td>
            <td>£{totalExp + totalBizTot}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td>Net profit or loss</td>
            <td></td>
            <td></td>
            <td>Box 47</td>
            <td>£{totalBizDis - (totalExp + totalBizTot)}</td>
          </tr>
        </tfoot>
      </table>
    </main>
  )
}
export default NetProfit
