import React,{ useContext } from 'react'
import { Link } from "react-router-dom"
import PayrollStateContext from '../../context/Payroll/PayrollStateContext'
import './BackLink.css'
const BackLink = ({path,txt})=>{
    const state = useContext(PayrollStateContext)
  return<Link to={state.base+path} className="BackLink blueLink"><i className="hi hi-chevron-left"/>{txt}</Link>
}
export default BackLink
