import React,{useContext} from 'react'
import SelfAssessmentDispatchContext from '../../context/SelfAssessment/SelfAssessmentDispatchContext'
import il_utl_checklist from '../../assets/images/il_utl_checklist.svg'
import './SelfAssessmentHome.css'
const SelfAssessmentHome = ()=>{
    const dispatch = useContext(SelfAssessmentDispatchContext)
  return (
    <main className="SelfAssessmentHome">
      <div><img src={il_utl_checklist} height="121" alt="Self assessment"/></div>
      <h1>Always know how much you owe</h1>
      <h2>Stay on top of your Self Assessment throughout the year.</h2>
      <div><button onClick={()=>dispatch({type:'OPEN_SA_MODAL'})} className="btn"><span>View Estimate</span></button></div>
      <div className="learnMore"><span className="blueLink">Learn more about doing Self Assessment with QuickBooks</span></div>
    </main>
  )
}
export default SelfAssessmentHome
