import React,{ useContext } from 'react'
import './ReadyForPayDay.css'
import PayrollDispatchContext from '../../../../context/Payroll/PayrollDispatchContext'

const ReadyForPayDay =()=>{
  const dispatch = useContext(PayrollDispatchContext)
  return (
  <main className="ReadyForPayDay">
    <header>
      <h1>Let's get your business ready for payday</h1>
      <h2>We'll guide you through the steps to accuartely and confidently pay your employees</h2>
    </header>
    <section>
      <article>
        <div className="imgCtn addInfos"></div>
        <div className="step">
          <span>Add your business info</span>
          <button className="btn" onClick={()=>dispatch({type:'openTrowser'})}><span>Let's go</span></button>
        </div>
      </article>
      <article><div className="imgCtn arrowUp"></div></article>
      <article>
        <div className="imgCtn payslips"></div>
        <div className="step">
          <span>Create Payslips</span>
        </div>
      </article>
      <article><div className="imgCtn arrowDown"></div></article>
      <article>
        <div className="imgCtn HMRC"></div>
        <div className="step">
          <span>File with HMRC</span>
        </div>
      </article>
    </section>
  </main>
)}
export default ReadyForPayDay
