import React,{useEffect,useState,useContext} from 'react'
import Thousands from '../../../Utils/Thousands'
import Helpful from '../Helpful'
import PayrollStateContext from '../../../context/Payroll/PayrollStateContext'

import './Breakdown.css'

const Breakdown = ()=> {
  const state = useContext(PayrollStateContext)
  const { breakdown,employees } = state
  const [ pay,setPay ] = useState(0)
  const PAYE = ()=> (pay * 5/100)
  const EMPLOYEENI = ()=> (pay * 9/100)
  const EMPLOYERNI = ()=> (pay * 11/100)

  const getAllEmployeePayments = emp => Object.keys(employees[emp].operations).reduce((sum,itm)=>{
    if (employees[emp].operations[itm].addition&&employees[emp].shouldGetPaid) return sum + employees[emp].operations[itm].amount
    return sum
  },0)
  const getAllEmployeeDeductions = emp => Object.keys(employees[emp].operations).reduce((sum,itm)=>{
    if (!employees[emp].operations[itm].addition&&employees[emp].shouldGetPaid) return sum + employees[emp].operations[itm].amount
    return sum
  },0)
  const setIt = ()=>{
    setPay(Object.keys(employees).reduce((sum,emp)=> sum + getAllEmployeePayments(emp)-getAllEmployeeDeductions(emp),0))
  }
  useEffect(()=>setIt())
  return (
    <main className={`Breakdown ${breakdown ? 'breakdown':''}`}>
      <article>
        <h3>Total gross pay</h3>
        <span>£{Thousands((pay-EMPLOYERNI()).toFixed(2))}</span>
      </article>
      <article>
        <label>PAYE</label>
        <span>£{Thousands(PAYE().toFixed(2))}</span>
      </article>
      <article>
        <label>Employee National Insurance</label>
        <span>£{Thousands(EMPLOYEENI().toFixed(2))}</span>
      </article>
      <article>
        <label>Employer National Insurance</label>
        <span>£{Thousands(EMPLOYERNI().toFixed(2))}</span>
      </article>
      <p className="fullBreakdown"><small>For the complete breakdown of monies owed, download the <span className="blueLink">payroll details (Gross to Net) report</span></small></p>
      <p className="rating"><span>Was this helpful?</span> <Helpful/></p>
    </main>
  )
}
export default Breakdown
